<template>
  <a-entity class="pano-point">
    <!-- TODO find out why color and src can't use v-bing: -->
    <a-image
      class="pano-point-image"
      src="/static/img/marker.b01ed52.png"
      v-for="marker in selectedPano.markers"
      v-if="marker.type === 'point' && !isPanoChanging"
      color="#82e6dc"
      :opacity="shouldMarkerBeActive(marker) ? activeOpacity : initialOpacity"
      rotation="0 0 0"
      width="22"
      height="22"
      :position="calcMarkersCoors(marker)"
      @click.prevent.stop="selectPano(marker, $event)"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)"
      cursor-listener
      look-at="[camera]"
      crossorigin="anonymous">
      <a-animation
        attribute="scale"
        from=".8 .8 1"
        to="1 1 1"
        dur="1500"
        fill="both"
        repeat="indefinite">
      </a-animation>
    </a-image>
    <a-entity
      color="#222"
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && selectedMarker.type === 'point'"
      geometry="primitive: plane; width: 80; height: 30"
      :position="calcMarkersCoors(marker, true)"
      draw="width: 194; height: 70;"
      htmltexture="asset: #point"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)"
      cursor-listener
      look-at="[camera]">
    </a-entity>
  </a-entity>
</template>

<script>
import utils from '../../utils'
import {
  panos,
  building,
  isTagClick,
  isPanoChanging,
  isMarkerActive,
  selectedMarker,
  selectedPano,
  hoveredPano,
  frontPanoEl,
  backPanoEl,
  cameraEl
} from '../../vuex/getters'

import {
  loadFullPano,
  setSelectedPano,
  setHoveredPano,
  setMarkerActive,
  setSelectedMarker,
  setCameraPosition,
  setPanoramaPosition,
  setCameraForwardRotation,
  setShouldUsePanoRotation
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      building,
      isTagClick,
      isPanoChanging,
      isMarkerActive,
      selectedMarker,
      selectedPano,
      hoveredPano,
      frontPanoEl,
      backPanoEl,
      cameraEl
    },
    actions: {
      loadFullPano,
      setSelectedPano,
      setHoveredPano,
      setMarkerActive,
      setSelectedMarker,
      setCameraPosition,
      setPanoramaPosition,
      setCameraForwardRotation,
      setShouldUsePanoRotation
    }
  },

  data () {
    return {
      markerImage: require('../../assets/marker.png'),
      // markerColor: '#82e6dc'
      initialOpacity: '.6',
      activeOpacity: '.8'
    }
  },

  methods: {
    calcMarkersCoors (marker, isTooltip) {
      let markerX = marker.x
      let markerY = marker.y
      let markerZ = marker.z
      if (isTooltip) {
        markerX = marker.x
        markerY = marker.y + 28
        markerZ = marker.z
      }
      return `${markerX} ${markerY} ${markerZ}`
    },

    selectPano (marker, event) {
      // if (this.isTagClick) return
      const pano = utils.findPanoById(this.panos, marker.nextPanoId)
      if (pano !== this.selectedPano) {
        this.setSelectedPano(pano)
        this.setShouldUsePanoRotation(false)
        this.loadFullPano(pano)
        this.setPanoramaPosition()
        this.setCameraForwardRotation(this.cameraEl, marker)
        this.setMarkerActive(false)
        document.getElementsByTagName('canvas')[0].style.cursor = 'default'
        // this.setCameraPosition(marker)
        utils.animateOpacity(pano, this.frontPanoEl, this.backPanoEl)
      }
    },

    markerOpacity (marker) {
      const pano = utils.findPanoById(this.panos, marker.nextPanoId)
      const isSelectedPano = pano === this.selectedPano
      const isHoveredPano = pano === this.hoveredPano
      // console.log('PANO: ', pano)
      // return (isSelectedPano || isHoveredPano)
      return (isSelectedPano || isHoveredPano)
        ? this.activeOpacity
        : this.initialOpacity
    },

    mouseenterMarker (marker, e) {
      // if (this.isTagClick) return
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
      // e.currentTarget.setAttribute('opacity', this.activeOpacity)
      document.getElementsByTagName('canvas')[0].style.cursor = 'pointer'
    },

    mouseleaveMarker (e) {
      // if (this.isTagClick) return
      // if (utils.isMobile()) return
      this.setMarkerActive(false)
      this.setSelectedMarker({})
      // e.currentTarget.setAttribute('opacity', this.initialOpacity)
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    },

    shouldMarkerBeActive (marker) {
      return this.isMarkerActive && marker === this.selectedMarker
    }
  }
}
</script>



