<template>
  <a-entity id="pano-terrain" rotation="0 0 0">
    <pano-nav-arrows v-if="isVRToggled"></pano-nav-arrows>
    <pano-tag v-if="isAppBuilding"></pano-tag>
    <pano-memo v-if="isAppBuilding"></pano-memo>
    <pano-point v-if="isAppBuilding"></pano-point>
    <pano-logo-tripod v-if="isAppBuilding"></pano-logo-tripod>
    <pano-tooltips v-if="!isAppBuilding"></pano-tooltips>
  </a-entity>
</template>

<script>
// import 'aframe-gif-shader'
// import 'aframe-text-component'
// const draw = require('aframe-draw-component').component
// const textwrap = require('aframe-textwrap-component').component
import 'aframe-look-at-component'
import PanoNavArrows from '../../components/PanoBackground/PanoNavArrows'
import PanoTooltips from '../../components/PanoBackground/PanoTooltips'
import PanoAnimation from '../../components/PanoBackground/PanoAnimation'
import PanoPoint from '../../components/PanoBackground/PanoPoint'
import PanoTag from '../../components/PanoBackground/PanoTag'
import PanoMemo from '../../components/PanoBackground/PanoMemo'
import PanoLogoTripod from '../../components/PanoBackground/PanoLogoTripod'

import {
  panos,
  selectedPano,
  hoveredPano,
  selectedCategory,
  markersOffset,
  terrainBaseSize,
  isVRToggled,
  cameraEl,
  frontPanoEl,
  backPanoEl,
  isAppBuilding
} from '../../vuex/getters'

import {
  setSelectedPano,
  setCameraPosition,
  setHoveredPano,
  loadFullPano
} from '../../vuex/actions'

export default {
  components: {
    PanoNavArrows,
    PanoTooltips,
    PanoAnimation,
    PanoPoint,
    PanoTag,
    PanoMemo,
    PanoLogoTripod
  },

  vuex: {
    getters: {
      panos,
      selectedPano,
      hoveredPano,
      selectedCategory,
      markersOffset,
      terrainBaseSize,
      isVRToggled,
      cameraEl,
      frontPanoEl,
      backPanoEl,
      isAppBuilding
    },
    actions: {
      setSelectedPano,
      setCameraPosition,
      setHoveredPano,
      loadFullPano
    }
  },

  data () {
    return {
      selectedColor: '#45b29d',
      initialOpacity: '.6',
      defaultOpacity: '.6',
      activeOpacity: '.8',
      markerSize: 58,
      planeWidth: 280,
      planeHeight: 280,
      planeY: 200,
      markerImage: require('../../assets/marker.png')
    }
  },

  ready () {

  },

  methods: {

  }
}
</script>



