api.version = '/api/v1'
api.serverURL = server.serverURL
api.baseURL = server.baseURL

api.fetchBuildingsByHash = (hash) => {
  return new Promise((resolve, reject) => {
    const FETCH_BUILDINGS_URL = api.serverURL + api.version + '/sharecode?code=' + hash
    Vue.http.get(FETCH_BUILDINGS_URL, {}).then(resp => {
      let obj = null
      if (resp.data[0]) obj = resp.data[0]
      else obj = {}
      console.log('BUILDINGS: ', resp.data)
      const building = {}
      building.name = obj.name
      building.floorplan = obj.floorplan
      building.logo = obj.logo
      building.logoPercent = obj.logoPercent
      building.music = obj.music
      building.shareCode = obj.sharecode
      building.user = obj.owner
      building.panos = getPanoramasFromBuilding(obj)
      resolve(building)
    }, err => {
      console.log('fetchBuildings error: ', err)
      reject(err)
    })
  })
}

api.fetchMusics = (hash) => {
  return new Promise((resolve, reject) => {
    const FETCH_BUILDINGS_URL = api.serverURL + api.version + '/music'
    Vue.http.get(FETCH_BUILDINGS_URL, {}).then(resp => {
      console.log('BUILDINGS: ', resp.data)
      const obj = resp.data[0]
      const building = {}
      building.name = obj.name
      building.floorplan = obj.floorplan
      building.music = obj.music
      building.logo = obj.logo
      building.music = obj.music
      building.shareCode = obj.sharecode
      building.user = obj.owner
      building.logoPercent = obj.logoPercent
      building.panos = getPanoramasFromBuilding(obj)
      resolve(building)
    }, err => {
      console.log('fetchBuildings error: ', err)
      reject(err)
    })
  })
}

function getPanoramasFromBuilding (obj) {
  const objPanoramas = []
  const panoramaResize = 0.7
  const panoramaOffset = 20
  const panoramas = obj.pano
  // let i = 0
  if (!panoramas) return
  panoramas.forEach(panorama => {
    console.log('PANORAMA:', panorama.panoRotation)
    const x = panorama.x * panoramaResize
    const y = (panorama.y + panoramaOffset) * panoramaResize
    const data = {
      id: panorama.objectId,
      name: panorama.name,
      updatedAt: panorama.updatedAt,
      category: panorama.categoryType,
      url: panorama.thumbnail,
      urlThumbnail: panorama.thumbnail,
      // urlLow: utils.resetImgSize(panorama.web, 2048, 1024),
      // urlLow: panorama.web,
      // urlHigh: panorama.web,
      urlLowVR: utils.resetImgSize(panorama.web),
      urlHighVR: utils.resetImgSize(panorama.web),
      urlLow: (utils.isMobile() ? utils.resetImgSize(panorama.web, 2048) : utils.resetImgSize(panorama.web)),
      urlHigh: (utils.isMobile() ? utils.resetImgSize(panorama.web, 2048) : utils.resetImgSize(panorama.web)),
      rotation: panorama.rotation || 0,
      panoRotation: panorama.panoRotation || 0,
      x: parseInt(x, 0),
      y: parseInt(y, 0),
      isDefault: panorama.isDefault || false,
      index: panorama.index,
      markers: panorama.markers || []
    }
    objPanoramas.push(data)
  })

  parseMarkers(objPanoramas)
  sortPanoramas(objPanoramas)
  return objPanoramas
}

function sortPanoramas (panoramas) {
  panoramas.sort((a, b) => {
    if (a.index > b.index) return 1
    if (a.index < b.index) return -1
    return 0
  })
}

