function parseMarkers (panoramas) {
  for (let i = 0, q = panoramas.length; i < q; i++) {
    const panorama = panoramas[i]
    for (let j = 0, k = panorama.markers.length; j < k; j++) {
      const marker = panorama.markers[j]
      if (marker.type === 'tag') {
        if (marker.action === 1) marker.actionText = 'Buy Now'
        if (marker.action === 2) marker.actionText = 'View at Home'
        if (marker.action === 3) marker.actionText = 'Learn More'
      }
      const depth = 1000
      const x = marker.x
      if (marker.isUnity) { // only parse Unity markers
        marker.x = -marker.z * depth
        marker.y = marker.y * depth
        marker.z = -x * depth
        marker.forward_y = marker.forward_y * (180 / Math.PI)
      }
    }
  }
  return panoramas
}

export default api



