<template>
  <div
    v-el:floorplan
    v-show="this.selectedCategory !== 'outdoors'"
    class="floorplan"
    :class="{ 'no-floorplan': !hasFloorplan }"
    :style="{ backgroundImage: 'url(' + building.floorplan + ')' }">
    <div
      v-for="pano in panos"
      v-show="isActiveCategory(pano)"
      class="marker"
      :class="{ 'hovered': pano === hoveredPano }"
      :style="setMarkerPosition(pano, offsetX, offsetY)"
      @mouseenter="setHoveredPano(pano)"
      @mouseleave="setHoveredPano(null)"
      @click="selectPano(pano)">
      <i class="fa fa-circle"></i>
      <i
        class="fa fa-circle"
        :style="setMarkerRotation(pano)">
      </i>
      <!-- <p>{{ $t(pano.category) }}</p> -->
    </div>
    <div class="location-marker" :style="locationMarkerPosition">
      <i class="fa fa-circle"></i>
      <i
        class="fa fa-circle"
        :style="locationMarkerRotation">
        <img :src="locationMarkerRange">
      </i>
    </div>
    <a
      v-el:toggle-button
      class="toggle-button"
      v-slide="slideRight">
      <i class="fa fa-chevron-right"></i>
    </a>
  </div>
</template>

<script>
import utils from '../utils'
import {
  panos,
  selectedPano,
  hoveredPano,
  building,
  selectedCategory,
  cameraRotation,
  markerRotation,
  frontPanoEl,
  backPanoEl
} from '../vuex/getters'

import {
  setSelectedPano,
  setCameraPosition,
  setHoveredPano,
  setShouldUsePanoRotation,
  loadFullPano
} from '../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      selectedPano,
      hoveredPano,
      building,
      selectedCategory,
      cameraRotation,
      markerRotation,
      frontPanoEl,
      backPanoEl
    },
    actions: {
      setSelectedPano,
      setCameraPosition,
      setHoveredPano,
      setShouldUsePanoRotation,
      loadFullPano
    }
  },

  data () {
    return {
      slideRight: {
        property: 'right',
        startValue: '0',
        amount: '-280px',
        startIcon: 'fa-2x fa-chevron-right',
        endIcon: 'fa-2x fa-chevron-left'
      },
      locationMarkerRange: require('../assets/range.png'),
      locationMarkerPosition: {
        top: 0,
        left: 0
      }
    }
  },

  computed: {
    offsetX () {
      const markerWidth = 20
      return (markerWidth / 2)
    },

    offsetY () {
      const markerHeight = 23
      return markerHeight
    },

    hasFloorplan () {
      return this.building.floorplan
    },

    locationMarkerRotation () {
      const locationMarkerRotation = -this.markerRotation
      return {
        transform: 'rotateZ(' + locationMarkerRotation + 'deg)'
      }
    }
  },

  watch: {
    selectedPano (newValue, oldValue) {
      this.setLocationMarkerPosition(newValue, oldValue)
    },

    panos () {
      if (utils.isMobile()) {
        this.$els.floorplan.style.right = '-280px'
        this.$els.toggleButton.children[0].className = 'fa fa-chevron-left'
      }
    }
  },

  methods: {
    selectPano (pano) {
      // TODO move selected thumb to the middle
      if (pano !== this.selectedPano) {
        // const selectedPano = this.panos[pano.index]
        this.setSelectedPano(pano)
        this.setShouldUsePanoRotation(false)
        this.loadFullPano(this.selectedPano)
        // this.setCameraPosition(pano)
        utils.animateOpacity(pano, this.frontPanoEl, this.backPanoEl)
      }
    },

    isActiveCategory (pano) {
      return utils.isActive(pano.category, this.selectedCategory)
    },

    setMarkerPosition (pano, offsetX, offsetY) {
      return {
        left: (pano.x - offsetX) + 'px',
        top: (pano.y - offsetY) + 'px'
      }
    },

    setMarkerRotation (pano) {
      const panoRotationDegrees = pano.rotation * (180 / Math.PI)
      return {
        transform: 'rotateZ(' + panoRotationDegrees + 'deg)'
      }
    },

    setLocationMarkerPosition (newValue, oldValue) {
      utils.isMobile()
        ? setLocationMarkerPositionMobile(this, newValue, oldValue)
        : setLocationMarkerPosition(this, newValue, oldValue)

      function setLocationMarkerPositionMobile (context, newValue, oldValue) {
        const newMarkerX = newValue.x - context.offsetX
        const newMarkerY = newValue.y - context.offsetY
        context.locationMarkerPosition.top = newMarkerY + 'px'
        context.locationMarkerPosition.left = newMarkerX + 'px'
      }

      function setLocationMarkerPosition (context, newValue, oldValue) {
        const startAnimation = {
          x: oldValue.x - context.offsetX,
          y: oldValue.y - context.offsetY
        }
        const endAnimation = {
          x: newValue.x - context.offsetX,
          y: newValue.y - context.offsetY
        }
        utils.animate({ startAnimation, endAnimation }, result => {
          context.locationMarkerPosition.top = result.y + 'px'
          context.locationMarkerPosition.left = result.x + 'px'
        })
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

$floorplan-size = 280px
$marker-size = 20px

.floorplan {
  background-color: $white
  border: 1px solid $white
  position: fixed
  top: 50%
  transform: translateY(-50%)
  right: 0
  width: $floorplan-size
  height: $floorplan-size
  background-size: 100% 100%
  z-index: 1

  &.no-floorplan {
    display: none !important
  }

  .toggle-button {
    cursor: pointer
    display: block
    position: absolute
    top: 50% // 20px minus half of button's width
    transform: translateY(-50%)
    left: -40px
    width: 48px
    height: 48px
    black-text-shadow()

    &:hover {
      i {
        brand-text-shadow()
      }
    }

    i {
      color: $white
      font-size: 18px
      position: absolute
      top: 50%
      left: 12px
      transform: translate(-50%,-50%)
    }
  }

  .marker {
    color: $white
    cursor: pointer
    opacity: .6
    position: absolute
    black-text-shadow()

    i {
      font-size: $marker-size

      &:first-child {
        color: darken($white, 30%)
        position: absolute
        text-shadow: 0 0 0 $default-color
      }
    }

    p {
      color: $white
      font-size: 13px
      position: absolute
      top: 0
      left: 0
      line-height: 10px
      text-align: center
      top: 22px
      left: 50%
      transform: translateX(-50%)
    }

    &.hovered {
      opacity: 1
      color: lighten($brand-color, 30%)
    }
  }

  .location-marker {
    position: absolute
    i {
      color: $brand-color
      font-size: $marker-size
      position: absolute

      &:first-child {
        color: darken($brand-color, 5%)
        position: absolute
        text-shadow: 0 0 0 $default-color
      }
    }

    img {
      opacity: .5
      position: absolute
      top: -39px
      left: 50%
      transform: translateX(-50%)
    }
  }
}
</style>



