<template>
  <div id="panos-wrapper">
    <a-scene
      id="panos"
      v-el:panos
      v-show="!showInstructions"
      vr-mode-ui="enabled: false"
      @mousedown="stopCameraAutoRotation">
      <a-sky
        v-el:back-pano
        class="pano"
        radius="7000"
        :position="joinedBackPanoPosition"
        :rotation="joinedPanoramaRotation"
        :src="backPano ? backPano.url : ''"
        :opacity="1">
      </a-sky>
      <a-sky
        v-el:front-pano
        class="pano"
        radius="7000"
        :position="joinedFrontPanoPosition"
        :rotation="joinedPanoramaRotation"
        :src="frontPano ? frontPano.url : ''"
        :opacity="frontPanoOpacity">
      </a-sky>
      <!-- aframe components don't have CSS, so display none doesn't work -->
      <!-- <pano-terrain v-if="!isVRToggled"></pano-terrain> -->
      <pano-terrain></pano-terrain>
      <pano-camera></pano-camera>
      <pano-q-r-code v-if="isQRCodeToggled"></pano-q-r-code>
    </a-scene>
    <!-- <div
      id="point-info"
      class="marker-info"
      v-show="isMarkerActive && selectedMarker.type === 'point'"
      :style="{ left: iconTooltipX + 'px', top: iconTooltipY + 'px' }">
      <div>{{ nextPanorama ? $t(nextPanorama.category) : null }}</div>
    </div> -->
    <div
      id="tag"
      class="marker-popup"
      :class="{ 'no-photo': !selectedMarker.photo, 'no-link': selectedMarker.action === 0 }">
      <img
        v-if="selectedMarker.photo"
        :src="selectedMarker.photo"
        class="pull-left"
        crossorigin="anonymous">
      <div class="marker-details pull-left">
        <div class="marker-details-top">
          <h3>{{ selectedMarker.title }}</h3>
          <p>{{ selectedMarker.description }}</p>
          <h4>{{ selectedMarker.price }}</h4>
        </div>
        <a
          class="btn btn-primary tag-action"
          :class="{ 'hovered': MarkerPopupButton === 'view', 'invisible': isVRToggled }"
          v-if="selectedMarker.action">
          {{ selectedMarker.actionText }}
        </a>
        <a
          class="btn btn-default tag-action"
          :class="{ 'hovered': MarkerPopupButton === 'close' }">
          {{ $t('close') }}
        </a>
      </div>
    </div>
    <div id="memo" class="marker-popup">
      <div class="marker-details pull-left">
        <div class="marker-details-top">
          <p>{{ selectedMarker.description }}</p>
        </div>
        <a
          class="btn btn-default tag-action"
          :class="{ 'hovered': MarkerPopupButton }">
          {{ $t('close') }}
        </a>
      </div>
    </div>
    <div id="point" class="marker-popup">
      <div class="marker-details pull-left">
        <div class="marker-details-top">
          <p>{{ nextPanorama ? $t(nextPanorama.category) : null }}</p>
        </div>
      </div>
    </div>
    <!-- <div
      id="memo-info"
      class="marker-info"
      :class="{ 'tag-mobile': isMobile() }"
      v-show="isMarkerActive && selectedMarker.type === 'memo'"
      :style="{ left: iconTooltipX + 'px', top: iconTooltipY + 'px' }">
      <i
        class="fa fa-close pull-right"
        @click="closeIcon">
      </i>
      <p>{{ selectedMarker.description }}</p>
    </div> -->
  </div>
</template>

<script>
import 'aframe-htmltexture-component'
import 'aframe-fit-texture-component'
import AFRAME from 'aframe'
import drawComponent from 'aframe-draw-component'
import utils from '../../utils'
import PanoCamera from '../../components/PanoBackground/PanoCamera'
import PanoTerrain from '../../components/PanoBackground/PanoTerrain'
import PanoQRCode from '../../components/PanoQRCode'
import {
  panos,
  backPano,
  frontPano,
  frontPanoOpacity,
  buildingHash,
  showInstructions,
  isVRToggled,
  isQRCodeToggled,
  isAppBuilding,
  isMarkerActive,
  isTagClick,
  MarkerPopupButton,
  shouldUsePanoRotation,
  selectedPano,
  selectedMarker,
  panoramaRotation,
  cameraPosition,
  frontPanoPosition,
  backPanoPosition,
  cameraEl
} from '../../vuex/getters'

import {
  setBackPano,
  setFrontPano,
  setSelectedPano,
  setCameraPosition,
  setSelectedMarker,
  setMarkerActive,
  loadFullPano,
  fetchBuildingsByHash,
  exitVR,
  exitFullscreen,
  increaseCameraRotation,
  stopCameraAutoRotation,
  setCameraStartRotation,
  getPanoEls,
  getPanosEl
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      backPano,
      frontPano,
      frontPanoOpacity,
      buildingHash,
      showInstructions,
      isVRToggled,
      isMarkerActive,
      isTagClick,
      isQRCodeToggled,
      isAppBuilding,
      MarkerPopupButton,
      shouldUsePanoRotation,
      selectedPano,
      selectedMarker,
      panoramaRotation,
      cameraPosition,
      frontPanoPosition,
      backPanoPosition,
      cameraEl
    },
    actions: {
      setBackPano,
      setFrontPano,
      setSelectedPano,
      setCameraPosition,
      setSelectedMarker,
      setMarkerActive,
      loadFullPano,
      fetchBuildingsByHash,
      exitVR,
      exitFullscreen,
      increaseCameraRotation,
      stopCameraAutoRotation,
      setCameraStartRotation,
      getPanoEls,
      getPanosEl
    }
  },

  components: {
    PanoCamera,
    PanoTerrain,
    PanoQRCode
  },

  data () {
    return {
      iconTooltipX: '',
      iconTooltipY: '',
      tagTooltipX: '',
      tagTooltipY: '',
      isFirstLoad: true
    }
  },

  computed: {
    nextPanorama () {
      if (!this.panos) return
      if (this.panos.length === 0) return
      const foundPanorama = this.panos.filter(pano => {
        return pano.id === this.selectedMarker.nextPanoId
      })
      return foundPanorama[0]
    },

    joinedFrontPanoPosition () {
      return `${this.frontPanoPosition.x} ${this.frontPanoPosition.y} ${this.frontPanoPosition.z}`
    },

    joinedBackPanoPosition () {
      return `${this.backPanoPosition.x} ${this.backPanoPosition.y} ${this.backPanoPosition.z}`
    },

    joinedPanoramaRotation () {
      // return `0 ${this.panoramaRotation} 0`
      return '0 0 0'
    },

    joinedCameraPosition () {
      return `${this.cameraPosition.x}
        ${this.cameraPosition.y}
        ${this.cameraPosition.z}`
    }
  },

  created () {
    this.fetchBuildingsByHash(this.buildingHash, () => {
      if (!this.panos) return
      const firstPano = this.panos[0]
      this.setBackPano(this.panos[0])
      this.setFrontPano(this.panos[0])
      this.loadFullPano(firstPano, this.panos)
      this.setSelectedPano(firstPano)
      if (!this.isAppBuilding) this.setCameraPosition(firstPano)
      // TODO testing only
      this.setCameraStartRotation(this.cameraEl)
      this.getPanosEl(this.$els.panos)
      this.getPanoEls(this.$els.frontPano, this.$els.backPano)
    })
  },

  ready () {
    AFRAME.registerComponent('draw', drawComponent.component)
    this.increaseCameraRotation()
    this.$els.panos.addEventListener('exit-vr', () => {
      this.increaseCameraRotation()
      this.exitFullscreen()
      this.exitVR()
    })

    this.$els.panos.addEventListener('mousemove', (e) => {
      // TODO move tooltip position coculate to click event
      // if (this.isTagClick) return
      this.iconTooltipX = e.clientX + 20
      this.iconTooltipY = e.clientY + 20
      this.tagTooltipX = e.clientX - 5
      this.tagTooltipY = e.clientY - 5
    })
  },

  methods: {
    isMobile () {
      return utils.isMobile()
    },

    closeIcon () {
      this.setMarkerActive(false)
      this.setSelectedMarker({})
      // e.currentTarget.setAttribute('opacity', this.initialOpacity)
    },

    dynamicMarkerUrl (selectedMarkerUrl) {
      const re = new RegExp('^(http|https)://', 'i')
      const hasHttps = re.test(selectedMarkerUrl)
      if (hasHttps) {
        return selectedMarkerUrl
      } else {
        return 'http://' + selectedMarkerUrl
      }
    }
  }
}
</script>

