import utils from '../utils'
import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'

Vue.use(Vuex)

const state = {
  translateX: 0,
  isMapCreated: false,
  buildingHash: {},
  building: {},
  user: {},
  panos: [],
  frontPano: {},
  frontPanoOpacity: 1,
  backPano: {},
  selectedPano: {},
  selectedMarker: {},
  tooltip: {},
  hoveredPano: null,
  categories: [],
  selectedCategory: '',
  cameraRotation: {
    x: 0,
    y: 0,
    z: 0
  },
  markerRotation: 0,
  panoramaRotation: 0,
  cameraPosition: {
    x: 0,
    y: 0,
    z: 0
  },
  isVRToggled: false,
  isQRCodeToggled: false,
  isFullScreen: false,
  showInstructions: false,
  thumbInterval: null,
  thumbTimeout: false,
  markersOffset: 140,
  terrainBaseSize: 18,
  cameraEl: null,
  cameraElRotation: null,
  cameraElPosition: null,
  thumbsEl: null,
  panosEl: null,
  frontPanoEl: null,
  backPanoEl: null,
  spinnerEl: null,
  commentsEl: null,
  rotationTween: null,
  rotationTimer: undefined,
  firebaseBuilding: null,
  firebaseComments: null,
  firebaseCurrMsg: '',
  isAuthModalActive: false,
  isFirebaseLiked: false,
  isFirebaseLoggedIn: false,
  firebaseUid: '',
  frontPanoPosition: {
    x: 0,
    y: 0,
    z: 0
  },
  backPanoPosition: {
    x: 0,
    y: 0,
    z: 0
  },
  isAppBuilding: true,
  isMarkerActive: false,
  isTagClick: false,
  isPanoChanging: false,
  shouldUsePanoRotation: false,
  MarkerPopupButton: false,
  isAutoRotatiing: true
}

const mutations = {
  SET_BUILDING (state, building) {
    state.building = building
  },

  SET_PANOS (state, panos) {
    state.panos = panos
  },

  SET_USER (state, user) {
    state.user = user
  },

  SET_BACK_PANO (state, pano) {
    state.backPano = pano
  },

  SET_FRONT_PANO (state, pano) {
    state.frontPano = pano
  },

  LOAD_FULL_PANO (state, pano) {
    pano.url = (state.isVRToggled) ? pano.urlHigh : pano.urlLow
  },

  // ANIMATE_OPACITY (state, pano) {
  //   let tweenPlaceholder = { opacity: 1 }
  //   new TWEEN.Tween(tweenPlaceholder)
  //     .to({ opacity: 0 }, 500)
  //     .onUpdate(function () {
  //       state.backPano.url = pano.url
  //       state.frontPanoOpacity = this.opacity
  //     })
  //     .onComplete(() => {
  //       state.frontPano.url = pano.urlLow
  //     })
  //     .start()
  //
  //   requestAnimationFrame(animate) // eslint-disable-line
  //   function animate (time) {
  //     requestAnimationFrame(animate) // eslint-disable-line
  //     TWEEN.update(time)
  //   }
  // },

  SET_PANORAMA_START_ROTATION (state) {
    // in other words reset the angle of the camera to 0
    const panoRotationDegrees = state.selectedPano.rotation * (180 / Math.PI)
    // state.panoramaRotation = -panoRotationDegrees + state.selectedPano.panoRotation
    const panoCalcRotation = -state.selectedPano.panoRotation + -panoRotationDegrees
    panoCalcRotation
      ? state.panoramaRotation = panoCalcRotation
      : state.panoramaRotation = 0
  },

  SET_MARKER_ROTATION (state, camera) {
    // minus sign needed because the direction of the editor is the opposite
    const panoRotationDegrees = state.selectedPano.rotation * (180 / Math.PI)
    const cameraRotation = camera.getAttribute('rotation').y
    // const startMarkerRotation = camera.parentElement.getAttribute('rotation').y - state.selectedPano.panoRotation
    // TODO find a way to get the markerRotation without having to substract
    // the selectedPano panoRotation
    let totalCameraRotation = 0
    if (camera.getAttribute('id') === 'camera') {
      let parentRotation = camera.parentNode.getAttribute('rotation').y
      totalCameraRotation = cameraRotation + panoRotationDegrees + parentRotation
    } else {
      totalCameraRotation = cameraRotation + panoRotationDegrees
    }
    // state.markerRotation = totalCameraRotation - state.selectedPano.panoRotation
    state.markerRotation = totalCameraRotation - panoRotationDegrees
  },

  SET_CAMERA_POSITION (state, newX, newY, newZ) {
    state.cameraPosition.x = newX
    state.cameraPosition.y = newY
    state.cameraPosition.z = newZ
  },

  SET_CAMERA_START_ROTATION (state, newY) {
    state.cameraRotation.y = newY
  },

  INCREASE_CAMERA_ROTATION (state, newY) {
    state.cameraRotation.y = newY
  },

  STOP_CAMERA_AUTO_ROTATION (state) {
    state.rotationTween.stop()
  },

  SET_ROTATION_TIMER (state, rotationTimer) {
    state.rotationTimer = rotationTimer
  },

  GET_BUILDING_HASH (state, buildingHash) {
    state.buildingHash = buildingHash
  },

  ENTER_VR (state) {
    state.isVRToggled = true
  },

  EXIT_VR (state) {
    state.isVRToggled = false
  },

  TOGGLE_VR (state) {
    state.isVRToggled = !state.isVRToggled
  },

  TOGGLE_QR_CODE (state) {
    state.isQRCodeToggled = !state.isQRCodeToggled
  },

  EXIT_FULL_SCREEN (state) {
    state.isFullScreen = false
  },

  TOGGLE_FULL_SCREEN (state) {
    state.isFullScreen = !state.isFullScreen
  },

  CLOSE_INTRUCTIONS (state) {
    state.showInstructions = false
  },

  TOGGLE_INSTRUCTIONS (state) {
    state.showInstructions = !state.showInstructions
  },

  SET_THUMBS_TRANSLATION (state, translateX) {
    const totalThumbs = utils.countChildren(state.thumbsEl)
    const thumbWidth = 144 // thumb width plus margin
    const slideLimit = (totalThumbs - 1) * thumbWidth
    state.translateX = translateX
    // set the translation limits
    if (-state.translateX < 0) state.translateX = 0
    if (-state.translateX > slideLimit) state.translateX = -slideLimit
  },

  CLEAR_SLIDER_THUMBS (state) {
    state.thumbInterval = false
  },

  SET_HOVERED_PANO (state, pano) {
    state.hoveredPano = pano
  },

  SET_SELECTED_PANO (state, pano) {
    state.selectedPano = pano
  },

  SET_SELECTED_CATEGORY (state, category) {
    state.selectedCategory = category
  },

  SET_TOOLTIP_POSITION (state, tooltip) {
    state.tooltip = tooltip
  },

  RESET_TRANSLATE_X (state) {
    state.translateX = 0
  },

  SET_PREV_NEXT_ARROWS (state) {
    const camera = state.cameraEl.parentElement
    const cameraRotation = camera.getAttribute('rotation')
    state.cameraElPosition = _.cloneDeep(camera.getAttribute('position'))
    state.cameraElRotation = _.cloneDeep(state.cameraEl.getAttribute('rotation'))
    state.cameraElRotation.x = -90
    // TODO find a way to remove this hack
    state.cameraElRotation.y = cameraRotation.y + state.cameraElRotation.y
  },

  GET_CAMERA_EL (state, cameraEl) {
    state.cameraEl = cameraEl
  },

  GET_THUMBS_EL (state, thumbsEl) {
    state.thumbsEl = thumbsEl
  },

  GET_PANOS_EL (state, panosEl) {
    state.panosEl = panosEl
  },

  GET_PANO_ELS (state, frontPanoEl, backPanoEl) {
    state.frontPanoEl = frontPanoEl
    state.backPanoEl = backPanoEl
  },

  GET_SPINNER_EL (state, spinnerEl) {
    state.spinnerEl = spinnerEl
  },

  GET_COMMENTS_EL (state, commentsEl) {
    state.commentsEl = commentsEl
  },

  FETCH_FIREBASE_BUILDING (state, building) {
    state.firebaseBuilding = building
  },

  FETCH_FIREBASE_COMMENTS (state, comments) {
    state.firebaseComments = comments
  },

  // TODO remove because these mutations are unnecessary?

  INCREASE_FIREBASE_VIEW_COUNT (state, viewCount) {
    state.firebaseBuilding.viewCount = viewCount + 1
  },

  INCREASE_FIREBASE_LIKE_COUNT (state, likeCount) {
    state.firebaseBuilding.likeCount = likeCount + 1
  },

  DECREASE_FIREBASE_LIKE_COUNT (state, likeCount) {
    state.firebaseBuilding.likeCount = likeCount - 1
  },

  UPDATE_FIREBASE_CURR_MESSAGE (state, message) {
    state.firebaseCurrMsg = message
  },

  RESET_FIREBASE_CURR_MESSAGE (state, message) {
    state.firebaseCurrMsg = ''
  },

  TOGGLE_AUTH_MODAL (state) {
    state.isAuthModalActive = !state.isAuthModalActive
  },

  SET_FIREBASE_LIKE (state, isLike) {
    state.isFirebaseLiked = isLike
  },

  SET_FIREBASE_LOGGED_IN (state, isLoggedIn) {
    state.isFirebaseLoggedIn = isLoggedIn
  },

  SET_FIREBASE_UID (state, uuid) {
    state.firebaseUid = uuid
  },

  SET_FRONT_PANO_POSITION (state, result) {
    state.frontPanoPosition = result
  },

  SET_BACK_PANO_POSITION (state, result) {
    state.backPanoPosition = result
  },

  SET_MARKER_ACTIVE (state, isMarkerActive) {
    state.isMarkerActive = isMarkerActive
  },

  SET_TAG_CLICK (state, isTagClick) {
    state.isTagClick = isTagClick
  },

  SET_SELECTED_MARKER (state, selectedMarker) {
    state.selectedMarker = selectedMarker
  },

  SET_SHOULD_USE_PANO_ROTATION (state, isSet) {
    state.shouldUsePanoRotation = isSet
  },

  SET_PANO_CHANGING (state, isPanoChanging) {
    state.isPanoChanging = isPanoChanging
  },

  SET_MARKER_POPUP_BUTTON (state, isSet) {
    state.MarkerPopupButton = isSet
  },

  LOGOUT (state) {
    state.isFirebaseLoggedIn = false
  }
}

export default new Vuex.Store({
  state,
  mutations
})



