<template>
  <div id="pano-social">
    <div class="reactions">
      <a>
        <i class="fa fa-eye"></i> {{ firebaseBuilding.viewCount }}
      </a>
      <a
        @click="onClickFirebaseLikeCount"
        :class="{ 'clicked': isFirebaseLiked }">
        <i class="fa fa-thumbs-up"></i> {{ firebaseBuilding.likeCount }}
      </a>
      <a @click="toggleMessaging">
        <i class="fa fa-comment"></i> {{ firebaseBuilding.commentCount }}
      </a>
    </div>
    <pano-messaging v-show="isMessagingActive"></pano-messaging>
  </div>
</template>

<script>
import PanoMessaging from '../../components/PanoSocial/PanoMessaging'
import firebase from 'firebase'
// import moment from 'moment'
import {
  building,
  buildingHash,
  firebaseBuilding,
  // firebaseComments,
  // firebaseCurrMsg,
  isFirebaseLiked,
  isFirebaseLoggedIn,
  firebaseUid,
  commentsEl
} from '../../vuex/getters'

import {
  authWithFirebase,
  increateFirebaseViewCount,
  onClickFirebaseLikeCount,
  fetchFirebaseBuilding,
  // fetchFirebaseComments,
  // addFirebaseComment,
  // updateFirebaseCurrMessage,
  toggleAuthModal,
  setFirebaseLoggedIn,
  setFirebaseLike,
  setFirebaseUid
  // getCommentsEl
} from '../../vuex/actions'

export default {
  name: 'PanoSocial',
  vuex: {
    getters: {
      building,
      buildingHash,
      firebaseBuilding,
      // firebaseComments,
      // firebaseCurrMsg,
      isFirebaseLiked,
      isFirebaseLoggedIn,
      firebaseUid,
      commentsEl
    },
    actions: {
      authWithFirebase,
      fetchFirebaseBuilding,
      // fetchFirebaseComments,
      // addFirebaseComment,
      increateFirebaseViewCount,
      onClickFirebaseLikeCount,
      // updateFirebaseCurrMessage,
      toggleAuthModal,
      setFirebaseLoggedIn,
      setFirebaseLike,
      setFirebaseUid
      // getCommentsEl
    }
  },

  components: {
    PanoMessaging
  },

  data () {
    return {
      // avatarImage: require('../../assets/avatar.png'),
      isMessagingActive: false
    }
  },

  ready () {
    const buildingHash = this.$route.params.buildingHash
    const buildingRef = firebase.database().ref('buildings/' + buildingHash)
    this.increateFirebaseViewCount(buildingHash)
    buildingRef.on('child_changed', snapshot => {
      this.fetchFirebaseBuilding(buildingHash)
    })
    this.initLocalStorageData()
  },

  methods: {
    initLocalStorageData () {
      const isLike = localStorage.getItem('istaging_sharevr_like' + '_' + this.buildingHash) // eslint-disable-line
      const isLoggedIn = localStorage.getItem('istaging_sharevr_logged_in') // eslint-disable-line
      const firebaseUid = localStorage.getItem('istaging_firebase_uid') // eslint-disable-line
      this.setFirebaseUid(firebaseUid)
      this.setFirebaseLike(JSON.parse(isLike) || false)
      this.setFirebaseLoggedIn(JSON.parse(isLoggedIn) || false)
    },

    toggleMessaging () {
      this.isMessagingActive = !this.isMessagingActive
      this.$nextTick(() => {
        this.commentsEl.scrollTop = this.commentsEl.scrollHeight
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

#pano-social {
  padding: 0
  position: fixed
  top: 42px
  left: 50%
  transform: translateX(-50%)
  z-index: 1
  color: $white
  max-width: 400px
  width: 100%

  a {
    color: $white
    cursor: pointer
    margin: 0 3px
    black-text-shadow()

    &:hover {
      brand-text-shadow()
      text-decoration: none
    }

    &.clicked {
      color: $brand-color
    }
  }

  .reactions {
    text-align: center
  }

  .messaging {
    background: alpha($default-color, 70%)
    border: 1px solid $white
    margin: 46px 0 0
    padding: 15px
    position: relative
    height: 400px

    h5 {
      margin: 0 0 4px
    }

    small {
      color: $gray-light
      margin-left: 4px
    }

    p {
      margin: 0 0 6px
    }

    form {
      position: absolute
      bottom: 15px
      left: 15px
      right: 15px
      width: auto
      max-width: 400 - 30px // minus padding x2
      black-box-shadow()
    }

    input[type="text"] {
      background: $white
      border-color: transparent
    }

    button {
      background: $brand-color
      border-color: transparent
      color: $white
      text-transform: uppercase
    }

    .comments {
      width: 397 - 30px
      height: 334px
      overflow-x: hidden
      overflow-y: auto
    }

    .comment {
      margin: 4px 0 0

      > img {
        display: inline-block
        margin: 0 5px 0 0
        vertical-align: top
      }

      .comment-details {
        width: 305px
        display: inline-block
      }
    }
  }
}
</style>



