<template>
  <div id="pano-modal" class="modal" tabindex="-1" role="dialog">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" @click="toggleAuthModal">
            <span aria-hidden="true">&times;</span>
          </button>
          <h4 class="modal-title">{{ $t('postComment') }}</h4>
        </div>
        <div class="modal-body">
          <p>{{ $t('selectAction') }}</p>
          <a
            class="btn btn-primary btn-facebook"
            @click="loginWithFacebook">
            <i class="fa fa-facebook"></i>
            {{ $t('loginFacebook') }}
          </a>
          <a
            class="btn btn-primary btn-google-plus"
            @click="loginWithGooglePlus">
            <i class="fa fa-google-plus"></i>
            {{ $t('loginGoogle') }}
          </a>
          <a class="post-anonymous" @click="postAsAnonymous">
            {{ $t('postAsAnonymous') }}
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  isAuthModalActive,
  isFirebaseLoggedIn,
  commentsEl
} from '../vuex/getters'

import {
  toggleAuthModal,
  authWithFirebase,
  setFirebaseLoggedIn,
  addFirebaseComment
} from '../vuex/actions'

export default {
  name: 'PanoContainer',
  vuex: {
    getters: {
      isAuthModalActive,
      isFirebaseLoggedIn,
      commentsEl
    },
    actions: {
      toggleAuthModal,
      authWithFirebase,
      setFirebaseLoggedIn,
      addFirebaseComment
    }
  },

  methods: {
    loginWithGooglePlus () {
      this.authWithFirebase('google-plus', () => {
        this.postComment()
      })
    },

    loginWithFacebook () {
      this.authWithFirebase('facebook', () => {
        this.postComment()
      })
    },

    postAsAnonymous () {
      this.postComment()
    },

    postComment () {
      this.toggleAuthModal()
      this.setFirebaseLoggedIn(true)
      localStorage.setItem('istaging_sharevr_logged_in', true) // eslint-disable-line
      this.addFirebaseComment()
      this.$nextTick(() => {
        this.commentsEl.scrollTop = this.commentsEl.scrollHeight
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../mixins'

$facebook-color = #3b5998
$google-plus-color = #d34836

#pano-modal {
  &.modal {
    display: block
    overflow: visible
  }

  .modal-content {
    text-align: center
  }

  .btn {
    display: block
    margin: 0 auto 6px
    width: 100%
    max-width: 200px
    black-box-shadow()

    > i {
      margin: 0 5px 0 0
    }
  }

  .btn-facebook {
    background: $facebook-color
    border-color: $facebook-color
  }

  .btn-google-plus {
    background: $google-plus-color
    border-color: $google-plus-color
  }

  .post-anonymous {
    display: block
    margin-top: 8px
  }
}
</style>



