<template>
  <a-entity>
    <!-- TODO find out why color and src can't use v-bing: -->
    <a-image
      src="/static/img/note.7182dbb.png"
      v-for="marker in selectedPano.markers"
      v-if="marker.type === 'memo' && !isPanoChanging"
      width="30"
      height="30"
      color="#82e6dc"
      transparent="true"
      cursor-listener
      look-at="[camera]"
      :position="calcMarkersCoors(marker)"
      rotation="0 0 0"
      :opacity="shouldMarkerBeActive(marker) ? activeOpacity : initialOpacity"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)"
      @click.prevent.stop="clickMarker(marker, $event) | debounce 200">
      <a-animation
        attribute="scale"
        from=".8 .8 1"
        to="1 1 1"
        dur="1500"
        fill="both"
        repeat="indefinite">
      </a-animation>
    </a-image>
    <a-entity
      color="#222"
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && selectedMarker.type === 'memo'"
      geometry="primitive: plane; width: 180; height: 70"
      :position="calcMarkersCoors(marker, true)"
      draw="width: 440; height: 166;"
      htmltexture="asset: #memo"
      look-at="[camera]"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)"
      cursor-listener>
    </a-entity>
    <a-box
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && marker.type === 'memo'"
      :position="calcMarkersCoors(marker, 'close')"
      opacity="0"
      width="52"
      height="14"
      depth="40"
      transparent="true"
      look-at="[camera]"
      @mouseenter="mouseenterButton(marker, 'close', $event)"
      @mouseleave="mouseleaveButton($event)"
      @click="closeMarkerPopup(marker)"
      cursor-listener>
    </a-box>
  </a-entity>
</template>

<script>
import utils from '../../utils'
import {
  panos,
  isTagClick,
  isVRToggled,
  isPanoChanging,
  isMarkerActive,
  selectedPano,
  selectedMarker
} from '../../vuex/getters'

import {
  setMarkerActive,
  setSelectedMarker,
  setMarkerPopupButton
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      isTagClick,
      isVRToggled,
      isPanoChanging,
      isMarkerActive,
      selectedPano,
      selectedMarker
    },
    actions: {
      setMarkerActive,
      setSelectedMarker,
      setMarkerPopupButton
    }
  },

  data () {
    return {
      memoImage: require('../../assets/note.png'),
      // memoColor: '#82e6dc',
      initialOpacity: '.6',
      activeOpacity: '0'
    }
  },

  ready () {
    setTimeout(() => {
      console.log('MAKER:', this.memoImage)
    }, 2000)
  },

  methods: {
    calcMarkersCoors (marker, element) {
      let markerX = marker.x
      let markerY = marker.y
      let markerZ = marker.z
      if (element === 'close') {
        markerX = marker.x
        markerY = marker.y - 19
        markerZ = marker.z
      }
      return `${markerX} ${markerY} ${markerZ}`
    },

    clickMarker (marker, e) {
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
    },

    mouseenterMarker (marker, e) {
      // if (this.isTagClick) return
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
      // e.currentTarget.setAttribute('opacity', this.activeOpacity)
      // document.getElementsByTagName('canvas')[0].style.cursor = 'pointer'
    },

    mouseleaveMarker (e) {
      // if (this.isTagClick) return
      // this.setMarkerActive(false)
      if (utils.isMobile()) return
      this.setMarkerActive(false)
      this.setSelectedMarker({})
      // e.currentTarget.setAttribute('opacity', this.initialOpacity)
      // document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    },

    mouseenterButton (marker, button, e) {
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
      this.setMarkerPopupButton(button)
      document.getElementsByTagName('canvas')[0].style.cursor = 'pointer'
    },

    mouseleaveButton (e) {
      this.setMarkerPopupButton('')
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    },

    shouldMarkerBeActive (marker) {
      return this.isMarkerActive && marker === this.selectedMarker
    },

    closeMarkerPopup (marker) {
      this.resetMarkerState()
    },

    resetMarkerState () {
      this.setMarkerActive(false)
      this.setSelectedMarker({})
      this.setMarkerPopupButton('')
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    }
  }
}
</script>



