import $ from 'jquery'

export const toggle = {
  update: function (toggle) {
    const $el = $(this.el)
    $el.click(() => {
      const $targetEl = $(toggle.targetEl)
      if ($targetEl.css('opacity') === '0') {
        $targetEl.css('visibility', 'visible')
        $targetEl.animate({ opacity: 1 }, 200)
      } else {
        // hide element when opacity animation is done
        $targetEl.animate({ opacity: 0 }, 200, () => {
          $targetEl.css('visibility', 'hidden')
        })
      }
    })
  }
}

export const toggleSiblings = {
  update: function (toggle) {
    const $el = $(this.el)
    $el.click(() => {
      const topSibl = toggle.topSibling
      const bottomSibl = toggle.bottomSibling
      const $topSibl = $(topSibl.selector)
      const $bottomSibl = $(bottomSibl.selector)
      if ($bottomSibl.css(toggle.direction) === bottomSibl.startValue) {
        $bottomSibl.animate({ [toggle.direction]: bottomSibl.endValue }, 200)
        $topSibl.animate({ [toggle.direction]: topSibl.startValue }, 200)
      } else {
        $bottomSibl.animate({ [toggle.direction]: bottomSibl.startValue }, 200)
        $topSibl.animate({ [toggle.direction]: topSibl.endValue }, 200)
      }
    })
  }
}

export const slide = {
  update: function (slide) {
    const $el = $(this.el)
    // TODO turn this function into smaller ones
    $el.click(() => {
      const startValue = parseInt($el.parent().css(slide.property), 10)
      const slideAmount = parseInt(slide.amount, 10)
      const slideStartValue = parseInt(slide.startValue, 10)
      let $mirror = null
      let mirrorStartValue = null
      if (slide.mirror) {
        $mirror = $(slide.mirror.target)
        mirrorStartValue = parseInt($mirror.css(slide.property), 10)
      }
      const calcValue = startValue + slideAmount
      const mirrorCalcValue = mirrorStartValue + slideAmount

      if (startValue === slideStartValue) {
        $el.parent().animate({ [slide.property]: calcValue }, 200)
        if (slide.startIcon) {
          $el.find('i').attr('class', `fa ${slide.endIcon}`)
        }
        if (slide.mirror) {
          $mirror.animate({ [slide.property]: mirrorCalcValue }, 200)
        }
      } else {
        $el.parent().animate({ [slide.property]: slideStartValue }, 200)
        if (slide.startIcon) {
          $el.find('i').attr('class', `fa ${slide.startIcon}`)
        }
        if (slide.mirror) {
          $mirror.animate({ [slide.property]: slide.mirror.startValue }, 200)
        }
      }
    })
  }
}

export const slideRelative = {
  update: function (slide) {
    const $el = $(this.el)
    $el.click(() => {
      const $navPos = $(slide.driver.selector).css(slide.direction)
      const $buttons = $el.parent()
      const $buttonsPos = $buttons.css(slide.direction)
      // animate buttons down
      if ($buttonsPos !== slide.target.endValue) {
        $buttons.animate({ [slide.direction]: slide.target.endValue }, 200)
        $el.find('i').attr('class', `fa ${slide.target.endIcon}`)
      }
      // animate buttons up
      if ($buttonsPos === slide.target.endValue) {
        if ($navPos === slide.driver.startPos) {
          $buttons.animate({ [slide.direction]: slide.target.startPos }, 200)
          $el.find('i').attr('class', `fa ${slide.target.startIcon}`)
        }
        if ($navPos === slide.driver.endPos) {
          $buttons.animate({ [slide.direction]: slide.target.endPos }, 200)
          $el.find('i').attr('class', `fa ${slide.target.startIcon}`)
        }
      }
    })
  }
}



