<template>
  <a-entity>
    <!-- TODO find out why color and src can't use v-bing: -->
    <a-image
      src="/static/img/tag.feb7d79.png"
      v-for="marker in selectedPano.markers"
      v-if="marker.type === 'tag' && !isPanoChanging"
      color="#82e6dc"
      geometry="primitive: plane"
      transparent="true"
      shader="flat"
      side="double"
      :opacity="shouldMarkerBeActive(marker) ? activeOpacity : initialOpacity"
      rotation="0 0 0"
      :position="calcMarkersCoors(marker, $event)"
      width="20"
      height="20"
      cursor-listener
      look-at="[camera]"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)",
      @click.prevent.stop="clickMarker(marker, $event) | debounce 200">
      <a-animation
        attribute="scale"
        from=".8 .8 1"
        to="1 1 1"
        dur="1500"
        fill="both"
        repeat="indefinite">
      </a-animation>
    </a-image>
    <a-entity
      color="#222"
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && selectedMarker.type === 'tag'"
      geometry="primitive: plane; width: 230; height: 100"
      :position="calcMarkersCoors(marker)"
      draw="width: 548; height: 276;"
      htmltexture="asset: #tag"
      @mouseenter="mouseenterMarker(marker, $event)"
      @mouseleave="mouseleaveMarker($event)"
      cursor-listener
      look-at="[camera]">
    </a-entity>
    <a-box
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && marker.type === 'tag' && marker.action !== 0"
      :position="calcMarkersCoors(marker, 'view')"
      opacity="0"
      width="52"
      height="14"
      depth="40"
      @mouseenter="mouseenterButton(marker, 'view', $event)"
      @mouseleave="mouseleaveButton($event)"
      @click="viewMarkerLink(marker)"
      cursor-listener
      look-at="[camera]">
    </a-box>
    <a-box
      v-for="marker in selectedPano.markers"
      v-if="marker === selectedMarker && marker.type === 'tag'"
      :position="calcMarkersCoors(marker, 'close')"
      opacity="0"
      width="52"
      height="14"
      depth="40"
      transparent="true"
      @mouseenter="mouseenterButton(marker, 'close', $event)"
      @mouseleave="mouseleaveButton($event)"
      @click="closeMarkerPopup(marker)"
      cursor-listener
      look-at="[camera]">
    </a-box>
  </a-entity>
</template>

<script>
// import 'aframe-href-component'
import utils from '../../utils'
import {
  isTagClick,
  isVRToggled,
  isPanoChanging,
  isMarkerActive,
  building,
  panos,
  selectedPano,
  selectedMarker
} from '../../vuex/getters'

import {
  setMarkerActive,
  setTagClick,
  setTooltipPostion,
  setSelectedMarker,
  setMarkerPopupButton
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      isTagClick,
      isVRToggled,
      isPanoChanging,
      isMarkerActive,
      building,
      panos,
      selectedPano,
      selectedMarker
    },
    actions: {
      setMarkerActive,
      setTagClick,
      setTooltipPostion,
      setSelectedMarker,
      setMarkerPopupButton
    }
  },

  data () {
    return {
      tagImage: require('../../assets/tag.png'),
      // tagColor: '#82e6dc',
      initialOpacity: '.6',
      activeOpacity: '0'
    }
  },

  methods: {
    calcMarkersCoors (marker, element) {
      let markerX = marker.x
      let markerY = marker.y
      let markerZ = marker.z
      if (element === 'view') {
        markerX = marker.x
        markerY = marker.y - 19
        markerZ = marker.z
      }
      if (element === 'close') {
        markerX = marker.x
        markerY = marker.y - 36
        markerZ = marker.z
      }
      return `${markerX} ${markerY} ${markerZ}`
    },

    clickMarker (marker, e) {
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
    },

    mouseenterMarker (marker, e) {
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
      // e.currentTarget.setAttribute('opacity', this.activeOpacity)
    },

    mouseleaveMarker (e) {
      if (utils.isMobile()) return
      this.resetMarkerState()
      // e.currentTarget.setAttribute('opacity', this.initialOpacity)
    },

    mouseenterButton (marker, button, e) {
      this.setMarkerActive(true)
      this.setSelectedMarker(marker)
      this.setMarkerPopupButton(button)
      document.getElementsByTagName('canvas')[0].style.cursor = 'pointer'
    },

    mouseleaveButton (e) {
      this.setMarkerPopupButton('')
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    },

    viewMarkerLink (marker) {
      console.log('TRIGGERED')
      const re = new RegExp('^(http|https)://', 'i')
      const hasHttps = re.test(marker.url)
      if (!this.isVRToggled) return
      if (hasHttps) {
        this.resetMarkerState()
        window.open(marker.url)
      } else {
        this.resetMarkerState()
        window.open('http://' + marker.url)
      }
    },

    closeMarkerPopup (marker) {
      this.resetMarkerState()
    },

    shouldMarkerBeActive (marker) {
      return this.isMarkerActive && marker === this.selectedMarker
    },

    resetMarkerState () {
      this.setMarkerActive(false)
      this.setSelectedMarker({})
      this.setMarkerPopupButton('')
      document.getElementsByTagName('canvas')[0].style.cursor = 'default'
    }
  }
}
</script>



