
<template>
  <div class="qr-code-container">
    <h4>{{ $t('qrCode') }}</h4>
    <canvas id="qr-code" v-el:qr-code></canvas>
    <p>{{ $t('scanQrCode') }}</p>
  </div>
</template>

<script>
import QRCodeLib from 'qrcode'
import api from '../api'
import { buildingHash } from '../vuex/getters'

export default {
  vuex: {
    getters: {
      buildingHash
    }
  },

  data () {
    return {
      url: api.baseURL + '/#!/' + this.buildingHash
    }
  },

  ready () {
    const qrCodeDraw = new QRCodeLib.QRCodeDraw()
    qrCodeDraw.draw(this.$els.qrCode, this.url, (error, canvas) => {
      if (error) return console.log('QR code error: ', error)
    })
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

.qr-code-container {
  position: fixed
  top: 50%
  left: 50%
  text-align: center
  transform: translate(-50%, -50%)
  z-index: 9999

  canvas {
    margin: 4px 0 8px
  }

  h4 {
    color: $white
    font-size: 18px
    text-align: center
    black-text-shadow()
  }

  p {
    color: $white
    text-align: center
    max-width: 280px
    black-text-shadow()
  }
}
</style>



