<template>
  <a-entity id="pano-tooltips">
    <a-plane
      v-for="pano in panos"
      v-if="shouldShowTooltip(pano)"
      :position="calcTooltipCoors(pano)"
      metalness=".1"
      width="220"
      height="210"
      :draw="'background:' + tooltipColor"
      :textwrap="setTooltipCategory($t(pano.category))"
      look-at="[camera]">
    </a-plane>
    <!-- TODO find a better way to display this -->
    <a-cone
      v-for="pano in panos"
      v-if="shouldShowTooltip(pano)"
      :position="calcTooltipCoors(pano, 'arrow')"
      radius-top="60"
      radius-bottom="1"
      metalness=".1"
      width="90"
      height="90"
      :draw="'background:' + tooltipColor"
      textwrap="text:"
      look-at="[camera]">
    </a-cone>
  </a-entity>
</template>

<script>
const draw = require('aframe-draw-component').component
const textwrap = require('aframe-textwrap-component').component
import {
  panos,
  hoveredPano,
  selectedPano,
  markersOffset,
  terrainBaseSize
} from '../../vuex/getters'

export default {
  vuex: {
    getters: {
      panos,
      hoveredPano,
      selectedPano,
      markersOffset,
      terrainBaseSize
    }
  },

  data () {
    return {
      planeY: 200,
      tooltipColor: '#333'
    }
  },

  ready () {
    AFRAME.registerComponent('draw', draw) // eslint-disable-line
    AFRAME.registerComponent('textwrap', textwrap) // eslint-disable-line
  },

  methods: {
    calcTooltipCoors (pano, element) {
      const offSet = 214
      let markerY = -(this.planeY - 2) + offSet
      let markerX = (pano.x - this.markersOffset) * this.terrainBaseSize
      let markerZ = (pano.y - this.markersOffset) * this.terrainBaseSize
      if (element === 'arrow') markerY -= 92
      if (element === 'back') {
        markerZ -= 1
        markerX -= 1
      }
      return `${markerX} ${markerY} ${markerZ}`
    },

    setTooltipCategory (category) {
      // TODO remove this hack
      let y = 0
      const twoLineCategories = [
        'Living room',
        'Dining room'
      ]
      twoLineCategories.indexOf(category) > -1 ? y = 128 : y = 148
      return `color: #fff;
        font: 52px sans-serif;
        line-height: 50px;
        text-align: center;
        x: 136;
        y: ${y};
        text: ${category};`
    },

    shouldShowTooltip (pano) {
      const isHoveredPano = pano === this.hoveredPano
      const isntSelectedPano = pano !== this.selectedPano
      return isHoveredPano && isntSelectedPano
    }
  }
}
</script>



