<template>
<div class="nav" v-el:nav>
  <div class="power-by in-nav" v-show="!ui">Powered by iStaging</div>
  <pano-thumbs></pano-thumbs>
  <a class="toggle-button" v-slide="slideDown" v-el:nav-slide>
    <i class="fa fa-chevron-down"></i>
  </a>
</div>
</template>

<script>
import PanoThumbs from '../../components/PanoNav/PanoThumbs'
import {
  panos
} from '../../vuex/getters'

export default {
  vuex: {
    getters: {
      panos
    }
  },

  components: {
    PanoThumbs
  },

  data () {
    return {
      slideDown: {
        property: 'bottom',
        startValue: '0px',
        amount: '-87px',
        startIcon: 'fa-chevron-down',
        endIcon: 'fa-chevron-up'
      }
    }
  },

  computed: {
    ui () {
      return this.$route.query.ui
    }
  },

  watch: {
    panos () {
      if (this.ui) {
        this.$els.nav.style.bottom = '-87px'
        console.log(this.$els)
        this.$els.navSlide.children[0].className = 'fa fa-chevron-up'
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.nav {
  left: 0
  bottom: 0
  position: fixed
  left: 50%
  transform: translateX(-50%)
  width: 100%
  max-width: 864px

  .toggle-button {
    /*background: $default-dark*/
    border-top-left-radius: $border-radius
    border-top-right-radius: $border-radius
    cursor: pointer
    display: block
    position: absolute
    left: 50%
    text-align: center // find out why need this
    transform: translateX(-50%)
    top: -22px
    width: 48px
    height: 48px
    black-text-shadow()

    &:hover {
      i {
        brand-text-shadow()
      }
    }

    i {
      color: $white
      font-size: 18px
    }
  }
}
.power-by {
  font-size: 16px

  &.in-nav {
    width: 100%
    left: 0
    top: -60px
    text-align: center
    display: block

    @media screen and (min-width: 1280px) {
      display: none
    }
  }
}
</style>



