<template>
  <a-entity
    class="pano-nav-arrows"
    v-if="isArrowReady"
    :position="calcArrowPosition"
    :rotation="calcArrowRotation">
    <a-ring
      v-for="arrow in arrows"
      :src="arrowImage"
      transparent="true"
      metalness=".1"
      :opacity="setArrowOpacity(arrow.direction)"
      :rotation="arrow.rotation"
      radius-inner="0.01"
      :radius-outer="arrowRadius"
      :position="arrow.position"
      @mouseenter="mouseenterArrow(arrow.direction, $event)"
      @mouseleave="mouseleaveArrow($event)"
      @click="arrow.action"
      cursor-listener>
    </a-ring>
    <!-- <a-ring
      v-for="arrow in arrows"
      :src=""
      color="black"
      transparent="true"
      metalness=".1"
      opacity=".1"
      :rotation="arrow.rotation"
      radius-inner="0.01"
      :radius-outer="arrowRadius"
      :position="arrow.shadowPosition"
      @mouseenter="mouseenterArrow(arrow.direction, $event)"
      @mouseleave="mouseleaveArrow($event)"
      @click="arrow.action"
      cursor-listener>
    </a-ring> -->
  </a-entity>
</template>

<script>
import utils from '../../utils'
import {
  panos,
  selectedPano,
  frontPanoEl,
  backPanoEl,
  cameraEl,
  cameraElPosition,
  cameraElRotation
} from '../../vuex/getters'

import {
  loadFullPano,
  setSelectedPano,
  setFrontPano,
  setBackPano,
  setCameraPosition,
  setCameraStartRotation,
  setShouldUsePanoRotation
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      selectedPano,
      frontPanoEl,
      backPanoEl,
      cameraEl,
      cameraElPosition,
      cameraElRotation
    },
    actions: {
      loadFullPano,
      setSelectedPano,
      setFrontPano,
      setBackPano,
      setCameraPosition,
      setCameraStartRotation,
      setShouldUsePanoRotation
    }
  },

  data () {
    return {
      isArrowReady: false,
      selectedColor: '#45b29d',
      arrowRadius: '.3',
      arrowImage: require('../../assets/nav-arrow.png'),
      arrows: [
        {
          direction: 'left',
          position: '-0.32 2 -1',
          shadowPosition: '-0.32 2 -1.01',
          rotation: '0 0 90',
          action: this.goToPrevMarker
        },
        {
          direction: 'right',
          position: '0.32 2 -1',
          shadowPosition: '0.32 2 -1.01',
          rotation: '0 0 -90',
          action: this.goToNextMarker
        }
      ]
    }
  },

  watch: {
    cameraElPosition: function (val, oldVal) {
      if (!val) return
      this.isArrowReady = true
      console.log('is arrow ready: ', this.isArrowReady)
    }
  },

  computed: {
    calcArrowPosition () {
      return `${this.cameraElPosition.x} ${this.cameraElPosition.y} ${this.cameraElPosition.z}`
    },

    calcArrowRotation () {
      return `${this.cameraElRotation.x} ${this.cameraElRotation.y} ${this.cameraElRotation.z}`
    }
  },

  ready () {

  },

  methods: {
    selectPano (pano, event) {
      if (pano !== this.selectedPano) {
        this.loadFullPano(pano, this.panos)
        this.setShouldUsePanoRotation(true)
        this.setBackPano(pano)
        this.setFrontPano(this.selectedPano)
        this.setSelectedPano(pano)
        // this.setCameraPosition(pano)
        this.setCameraStartRotation(this.cameraEl)
        utils.animateOpacity(pano, this.frontPanoEl, this.backPanoEl)
      }
    },

    mouseenterArrow (direction, event) {
      const lastPano = this.panos[this.panos.length - 1]
      const isLastPano = this.selectedPano.index === lastPano.index
      if (direction === 'left' && this.selectedPano.index === 0) return
      if (direction === 'right' && isLastPano) return
      event.currentTarget.setAttribute('color', this.selectedColor)
    },

    mouseleaveArrow (event) {
      event.currentTarget.setAttribute('color', '')
    },

    setArrowOpacity (direction) {
      const activeOpacity = '.4'
      const disabledOpacity = '.14'
      if (direction === 'left') {
        return (this.selectedPano.index === 0)
          ? disabledOpacity
          : activeOpacity
      }
      if (direction === 'right' && this.panos.length > 0) {
        const lastPano = this.panos[this.panos.length - 1]
        return (this.selectedPano.index === lastPano.index)
          ? disabledOpacity
          : activeOpacity
      }
    },

    goToPrevMarker (event) {
      if (this.selectedPano.index !== 0) {
        const prevPano = this.selectedPano.index - 1
        this.selectPano(this.panos[prevPano])
        if (this.selectedPano.index === 0) {
          event.currentTarget.setAttribute('color', '')
        }
      }
    },

    goToNextMarker (event) {
      const lastPano = this.panos[this.panos.length - 1]
      if (this.selectedPano.index !== lastPano.index) {
        const nextPano = this.selectedPano.index + 1
        this.selectPano(this.panos[nextPano])
        if (this.selectedPano.index === lastPano.index) {
          event.currentTarget.setAttribute('color', '')
        }
      }
    }
  }
}
</script>



