<template>
  <div id="pano-user">
    <img
      class="img-circle"
      v-if="user && user.profileUrl"
      :src="user.profileUrl">
    <div class="pano-user-details">
      <h4 v-if="user && user.name">
        {{ user.name }}
        <br>
        <small v-if="user && user.company">
          {{ user.company }}
        </small>
      </h4>
      <p v-if="user && user.phone">{{ user.phone }}</p>
    </div>
  </div>
</template>

<script>
import {
  user
} from '../vuex/getters'

export default {
  name: 'PanoUser',
  vuex: {
    getters: {
      user
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

#pano-user {
  padding: 0
  position: fixed
  left: 15px
  top: 15px
  z-index: 1

  img {
    border: 1px solid $white
    width: 48px
    height: 48px
  }

  .pano-user-details {
    h4,
    small,
    p {
      color: $white
      margin: 0
      heavy-text-shadow()
    }

    h4 {
      font-size: 16px
      margin-top: 5px
    }

    small {
      text-transform: uppercase
    }
  }
}
</style>



