<template>
  <div class="map">
    <div id="map"></div>
  </div>
</template>

<script>
// import $ from 'jquery'
// import store from '../store'
import GoogleMapsLoader from 'google-maps'

export default {
  data () {
    return {
      map: {
        latitude: 51.5073509,
        longitude: -0.12775829999998223,
        infoWindow: '<strong>Google Map</strong><br>London, United Kingdom<br>'
      }
    }
  },

  ready () {
    GoogleMapsLoader.load(google => {
      const options = {
        zoom: 10,
        center: new google.maps.LatLng(this.map.latitude, this.map.longitude),
        mapTypeId: google.maps.MapTypeId.ROADMAP
      }
      const map = new google.maps.Map(document.getElementById('map'), options)
      const marker = new google.maps.Marker({
        map: map,
        position: new google.maps.LatLng(this.map.latitude, this.map.longitude)
      })
      const infowindow = new google.maps.InfoWindow({
        content: this.map.infoWindow
      })

      google.maps.event.addListener(marker, 'click', function () {
        infowindow.open(map, marker)
      })
      infowindow.open(map, marker)

      google.maps.event.addDomListener(window, 'resize', function () {
        var center = map.getCenter()
        google.maps.event.trigger(map, 'resize')
        map.setCenter(center)
      })
    })
  },

  methods: {
    createDestroyMap () {
      // store.createDestroyMap()
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

.map {
  font-size: 26px
  position: fixed
  left: 50%
  top: 50%
  transform: translate(-50%, -50%)
  z-index: 1
  box-shadow(1px)

  .close-button {
    color: $default-dark
    cursor: pointer
    position: absolute
    top: 10px
    right: 10px
    z-index: 1
  }
}

#map {
  width: 600px
  height: 600px
}
</style>



