utils.toggleFullScreen = () => {
  if (!document.fullscreenElement &&    // alternative standard method
    !document.mozFullScreenElement && !document.webkitFullscreenElement && !document.msFullscreenElement) {
    if (document.documentElement.requestFullscreen) {
      document.documentElement.requestFullscreen()
    } else if (document.documentElement.msRequestFullscreen) {
      document.documentElement.msRequestFullscreen()
    } else if (document.documentElement.mozRequestFullScreen) {
      document.documentElement.mozRequestFullScreen()
    } else if (document.documentElement.webkitRequestFullscreen) {
      document.documentElement.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT) // eslint-disable-line
    }
  } else {
    if (document.exitFullscreen) {
      document.exitFullscreen()
    } else if (document.msExitFullscreen) {
      document.msExitFullscreen()
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen()
    } else if (document.webkitExitFullscreen) {
      document.webkitExitFullscreen()
    }
  }
}

utils.exitFullscreen = () => {
  if (document.exitFullscreen) {
    document.exitFullscreen()
  } else if (document.msExitFullscreen) {
    document.msExitFullscreen()
  } else if (document.mozCancelFullScreen) {
    document.mozCancelFullScreen()
  } else if (document.webkitExitFullscreen) {
    document.webkitExitFullscreen()
  }
}

utils.selectActive = (target, activeClass) => {
  $(`.${activeClass}`).removeClass(activeClass)
  $(target).addClass(activeClass)
}

utils.resetFullImages = (others, target) => {
  others.forEach(other => {
    if (other !== target) other.url = other.urlLow
  })
}

utils.deselectSelect = (others, target) => {
  $(others).hide()
  $(target).show()
}

utils.toggleIcon = (startIcon, endIcon, event) => {
  const $el = $(event.currentTarget)
  if ($el.find('i').attr('class') === endIcon) {
    $el.find('i').attr('class', startIcon)
  } else {
    $el.find('i').attr('class', endIcon)
  }
}

utils.toggleAll = (driver, elements, button) => {
  const $navDisplay = $(driver).css('display')
  const $uiElements = $(elements)
  const $vrButton = $(button)
  if ($navDisplay === 'none') {
    $uiElements.show()
    $vrButton.css('bottom', '150px')
  } else {
    $uiElements.hide()
    $vrButton.css('bottom', '20px')
  }
}

// TODO split into smaller functions

utils.animateOpacity = (pano, frontPanoEl, backPanoEl) => {
  let tweenPlaceholder = { opacity: 1 }
  new TWEEN.Tween(tweenPlaceholder)
    .to({ opacity: 0 }, 1000)
    .onUpdate(function () {
      backPanoEl.setAttribute('src', pano.url)
      frontPanoEl.setAttribute('opacity', this.opacity)
    })
    .onComplete(function () {
      frontPanoEl.setAttribute('src', pano.urlLow)
    })
    .start()

  requestAnimationFrame(animate) // eslint-disable-line
  function animate (time) {
    requestAnimationFrame(animate) // eslint-disable-line
    TWEEN.update(time)
  }
}

utils.countChildren = (element) => {
  const children = [].slice.call(element.children)
  const newChildren = []
  children.forEach(child => {
    if (child.style.display !== 'none') {
      newChildren.push(child)
    }
  })
  return newChildren.length
}

