utils.animate = (options, callback, completed) => {
  const speed = options.speed || 1000
  const tween = new TWEEN.Tween(options.startAnimation)
    .to(options.endAnimation, speed)
    .onUpdate(function () {
      if (callback) callback(this, tween)
    })
    .onComplete(function () {
      if (completed) completed()
    })
    .start()
  if (options.repeat) tween.repeat(Infinity)
  if (!options.noEasing) tween.easing(TWEEN.Easing.Cubic.Out)
  requestAnimationFrame(animate) // eslint-disable-line
}

utils.getBrowserLang = () => {
  return navigator.languages
    ? navigator.languages[0]
    : (navigator.language || navigator.userLanguage)
}

utils.setLang = (lang) => {
  let selectedLang = ''
  const english = ['en', 'en-us', 'en-uk']
  const chinese = ['zh', 'zh-tw', 'zh-cn']
  if (english.indexOf(lang.toLowerCase()) > -1) selectedLang = 'en'
  if (chinese.indexOf(lang.toLowerCase()) > -1) selectedLang = 'zh'
  Vue.config.lang = selectedLang
}

utils.findPanoById = (panos, currPanoId) => {
  return panos.filter(pano => pano.id === currPanoId)[0]
}

utils.resetImgSize = (imgSrc, w) => {
  return (w ? imgSrc.replace('w/4096/h/2048/q/100', 'w/' + w + '/q/80')
            : imgSrc.replace('w/4096/h/2048/q/100', 'w/4096/q/80'))
}

function animate (time) {
  requestAnimationFrame(animate) // eslint-disable-line
  TWEEN.update(time)
}

export default utils



