<template>
<ul class="categories no-bullet">
  <!-- <li v-for="category in categories">
    <a
      class="category"
      :class="{ 'pressed': category === selectedCategory }"
      @click="setSelectedCategory(category)">
      {{ $t(category) }}
    </a>
  </li> -->
</ul>
</template>

<script>
import _ from 'lodash'
import { panos, selectedCategory } from '../vuex/getters'
import { setSelectedCategory } from '../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      selectedCategory
    },
    actions: {
      setSelectedCategory
    }
  },

  computed: {
    categories () {
      const categories = _(this.panos)
        .map(pano => pano.category)
        .uniq()
        .value()
      categories.unshift('all')
      return categories
    }
  },

  ready () {
    const firstCategory = this.categories[0]
    this.setSelectedCategory(firstCategory)
  }
}
</script>
<style lang="stylus">
@import '../variables'
@import '../mixins'

.categories {
  padding: 0
  position: fixed
  left: 15px
  top: 15px
  z-index: 1

  .category {
    color: $white
    cursor: pointer
    black-text-shadow()

    &:hover {
      brand-text-shadow()
      text-decoration: none
    }

    &.pressed {
      color: $brand-color
    }
  }
}
</style>



