<template>
  <div class="buttons">
    <button
      class="toggle-fullscreen toggle-button"
      @click="handleToggleFullScreen">
      <!-- <i v-if="!isFullScreen" class="fa fa-arrows-alt"></i> -->
      <i :class="['fa', fullScreenIcons]"></i>
    </button>
    <button
      v-show="!isFullScreen"
      class="toggle-volume toggle-button"
      :class="{ 'disabled': this.building.music === 'none' }"
      @click="toggleVolume">
      <i :class="['fa', getVolumeClass]"></i>
      <audio
        loop
        autoplay="true"
        id="audioMusic"
        v-if="building.music && building.music !== 'none'"
        :src="building.music" type="audio/mpeg">
    </button>
    <button
      v-if="!isFullScreen"
      class="toggle-vr toggle-button"
      :class="{ 'disabled': isDesktop }"
      @click="toggleVR"
      @mouseover="toggleTooltip('show')"
      @mouseout="toggleTooltip('hide')">
      <i class="fa fa-simplybuilt"></i>
    </button>
    <button
      v-if="!isFullScreen"
      class="toggle-qrcode toggle-button"
      @click="toggleQRCode">
      <!-- <i v-if="!isFullScreen" class="fa fa-arrows-alt"></i> -->
      <i class="fa fa-qrcode"></i>
    </button>
    <div
      class="toggle-vr-tooltip"
      v-show="isTooltipVisible">
      <p>{{ $t('vrTooltip') }}</p>
      <i class="fa fa-caret-right"></i>
    </div>
    <!-- use wrapper to prevent modifying elements' original opacity -->
    <!-- <div class="map-wrapper">
      <button
        class="toggle-map toggle-button"
        @click="createDestroyMap($event)"
      > <i class="fa fa-map"></i> </button>
    </div> -->
  </div>
</template>

<script>
import utils from '../utils'
import {
  building,
  isFullScreen
} from '../vuex/getters'

import {
  toggleVR,
  toggleFullScreen,
  toggleQRCode
} from '../vuex/actions'

export default {
  vuex: {
    getters: {
      building,
      isFullScreen
    },
    actions: {
      toggleVR,
      toggleFullScreen,
      toggleQRCode
    }
  },

  data () {
    return {
      isTooltipVisible: false,
      isVolume: true,
      toggleMap: {
        targetEl: '.map'
      },
      slideRight: {
        property: 'right',
        startValue: '35px',
        amount: '-35px'
      }
    }
  },

  computed: {
    isDesktop () {
      return !utils.isMobile()
    },

    fullScreenIcons () {
      return (this.isFullScreen) ? 'fa-close' : 'fa-arrows-alt'
    },

    getVolumeClass () {
      return (this.isVolume) ? 'fa-volume-up' : 'fa-volume-off'
    }
  },

  watch: {
    building (val, oldVal) {
      if (!val || !utils.isMobile()) return
      let lastSeen
      const loop = function () {
        lastSeen = Date.now()
        setTimeout(loop, 50)
      }
      loop()

      const music = document.getElementById('audioMusic')
      music.addEventListener('timeupdate', function () {
        if (Date.now() - lastSeen > 100) {
          this.pause()
        }
      }, false)
    }
  },

  ready () {
    if (utils.isMobile()) this.isVolume = false
  },

  methods: {
    // createDestroyMap ($event) {
    //   store.createDestroyMap($event)
    // },
    toggleTooltip (action) {
      if (!utils.isMobile()) {
        if (action === 'show') this.isTooltipVisible = true
        if (action === 'hide') this.isTooltipVisible = false
      }
    },

    toggleVolume () {
      if (this.building.music === 'none') return
      if (this.isVolume) {
        document.getElementById('audioMusic').pause()
        this.isVolume = false
      } else {
        document.getElementById('audioMusic').play()
        this.isVolume = true
      }
    },

    handleToggleFullScreen () {
      this.toggleFullScreen()
      if (!this.isFullScreen) document.getElementById('audioMusic').play()
    }

    // getMusicSrc () {
    //   return require('../assets/music/' + this.building.music)
    // }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

audio {
   display:
   none
}

.buttons {
  position: fixed
  top: 8px
  right: 4px
  z-index: 1

  .toggle-button {
    background: none
    border: 0
    color: $white
    display: block
    padding: 0
    width: 48px
    height: 36px
    black-text-shadow()

    &:hover {
      i {
        brand-text-shadow()
      }
    }

    i {
      font-size: 18px
    }
  }

  .toggle-vr-tooltip {
    background: $default-color
    padding: 5px 10px
    position: absolute
    top: 75px
    right: 46px

    p {
      color: $white
      font-size: 13px
      margin: 0
      white-space: nowrap
    }

    i {
      color: $default-color
      font-size: 18px
      position: absolute
      top: 50%
      right: -6px
      transform: translateY(-50%)
    }
  }

  .toggle-qrcode {
    i {
      font-size: 22px
    }
  }

  /*.toggle-volume {
    .disabled {
      cursor: default
    }
  }*/
}
</style>



