<template>
  <a-animation
    attribute="radius-outer"
    :dur="markerAnimationDur"
    :from="markerSize"
    to="10"
    repeat="indefinite"
    easing="linear">
  </a-animation>
  <a-animation
    attribute="opacity"
    :dur="markerAnimationDur"
    from="1"
    to="0"
    repeat="indefinite"
    easing="linear">
  </a-animation>
</template>

<script>
export default {
  props: {
    markerSize: 0
  },

  data () {
    return {
      markerAnimationDur: 1000
    }
  }
}
</script>



