export const fetchBuildingsByHash = ({ dispatch, state }, hash, callback) => {
  api.fetchBuildingsByHash(hash).then(building => {
    dispatch('SET_BUILDING', building)
    dispatch('SET_PANOS', building.panos)
    dispatch('SET_USER', building.user)
    /* eslint-disable */
    ga('send', {
      hitType: 'pageview',
      page: location.hash
    })
    ga('send', {
      hitType: 'event',
      eventCategory: 'Building',
      eventAction: 'View',
      eventLabel: building.shareCode
    })
    /* eslint-enable */
    callback()
  })
}

