export const loadFullPano = ({ dispatch, state }, pano) => {
  if ((state.isVRToggled && pano.url === pano.urlHigh) || !state.isVRToggled && pano.url === pano.urlLow) return
  // if (pano.url !== pano.urlHigh && state.isVRToggled) {
  const image = new Image() // eslint-disable-line
  const imageHigh = new Image() // eslint-disable-line

  dispatch('SET_PANO_CHANGING', true)
  image.src = pano.url
  image.onload = () => { dispatch('LOAD_FULL_PANO', pano) }
  state.spinnerEl.style.display = 'block'
  imageHigh.src = (state.isVRToggled) ? pano.urlHigh : pano.urlLow
  imageHigh.onload = () => {
    setTimeout(function () {
      dispatch('SET_PANO_CHANGING', false)
      state.spinnerEl.style.display = 'none'
    }, 1000)
  }
  // }
}

