export const toggleVR = ({ dispatch, state }) => {
  if (utils.isMobile()) {
    if (state.isVRToggled) {
      state.panosEl.exitVR()
      utils.exitFullscreen() // aframe isn't exiting fullscreen for some reason
    } else {
      state.panosEl.enterVR()
      dispatch('STOP_CAMERA_AUTO_ROTATION')
      setTimeout(() => { dispatch('SET_PREV_NEXT_ARROWS') }, 1000)
    }
    dispatch('TOGGLE_VR')
    if (state.isVRToggled) {
      state.frontPano.url = state.frontPano.urlHighVR
      state.backPano.url = state.backPano.urlHighVR
    } else {
      state.frontPano.url = state.frontPano.urlLow
      state.backPano.url = state.frontPano.urlLow
    }
    if (utils.isPortrait() && !utils.isAndroid()) {
      dispatch('TOGGLE_INSTRUCTIONS')
    }
  }
}

