export const backToShowcase = ({ dispatch, state }) => {
  if (state.isVRToggled && !utils.isAndroid()) {
    dispatch('TOGGLE_INSTRUCTIONS')
  }
  state.panosEl.exitVR()
  utils.exitFullscreen() // aframe isn't exiting fullscreen for some reason
  dispatch('EXIT_VR')
  dispatch('CLOSE_INTRUCTIONS')
  startCameraAutoRotation(state, resultY => {
    dispatch('INCREASE_CAMERA_ROTATION', resultY)
  })
}

export const slideThumbs = ({ dispatch, state }, arrow) => {
  const thumbsWidth = 144 * 2 // two thumbs width
  const startAnimation = { x: state.translateX }
  let endAnimation = null
  if (arrow === 'left') endAnimation = { x: state.translateX + thumbsWidth }
  if (arrow === 'right') endAnimation = { x: state.translateX - thumbsWidth }
  utils.animate({ startAnimation, endAnimation, speed: 400 }, result => {
    dispatch('SET_THUMBS_TRANSLATION', result.x)
  })
}

