export const setCameraPosition = ({ dispatch, state }, pano) => {
  const cameraStartY = 1.8
  const cameraEndX = (pano.x - state.markersOffset) * state.terrainBaseSize
  const cameraEndZ = (pano.y - state.markersOffset) * state.terrainBaseSize
  const startAnimation = {
    x: state.cameraPosition.x,
    y: cameraStartY,
    z: state.cameraPosition.z
  }
  const endAnimation = { x: cameraEndX, y: cameraStartY, z: cameraEndZ }
  TWEEN.removeAll() // so the tweens don't overlap
  if (utils.isMobile()) {
    dispatch('SET_CAMERA_POSITION', cameraEndX, cameraStartY, cameraEndZ)
    Vue.nextTick(() => {
      // dispatch('SET_PREV_NEXT_ARROWS')
      setTimeout(() => { dispatch('SET_PREV_NEXT_ARROWS') }, 1000)
    })
  } else {
    utils.animate({ startAnimation, endAnimation }, result => {
      dispatch('SET_CAMERA_POSITION', result.x, result.y, result.z)
      // dispatch('SET_PREV_NEXT_ARROWS')
      setTimeout(() => { dispatch('SET_PREV_NEXT_ARROWS') }, 1000)
    })
  }
}

export const setPanoramaPosition = ({ dispatch, state }) => {
  const zoom = 0
  const startFrontPanoAnimation = { x: 0, y: 0, z: 0 }
  const startBackPanoAnimation = { x: 0, y: 0, z: zoom }
  const endFrontPanoAnimation = {
    x: state.cameraPosition.x,
    y: state.cameraPosition.y,
    z: state.cameraPosition.z - zoom
  }
  const endBackPanoAnimation = { x: 0, y: 0, z: 0 }
  utils.animate({
    startAnimation: startFrontPanoAnimation,
    endAnimation: endFrontPanoAnimation,
    speed: 1000
  }, result => {
    dispatch('SET_FRONT_PANO_POSITION', result)
  })
  utils.animate({
    startAnimation: startBackPanoAnimation,
    endAnimation: endBackPanoAnimation,
    speed: 1000
  }, result => {
    dispatch('SET_BACK_PANO_POSITION', result)
  })
}

export const setCameraStartRotation = ({ dispatch, state }, camera) => {
  // const panoRotationDegrees = state.selectedPano.rotation * (180 / Math.PI)
  const cameraRotationY = camera ? camera.getAttribute('rotation').y : 0
  // panoRotation used to modify the a-sky element
  // const totalCameraRotation = -panoRotationDegrees - cameraRotationY
  const totalCameraRotation = -cameraRotationY
  const cameraEndRotationY = totalCameraRotation + state.selectedPano.panoRotation
  const startAnimation = { y: state.cameraRotation.y }
  const endAnimation = { y: cameraEndRotationY }
  // resetCameraAutoRotation(state)
  TWEEN.removeAll() // so the tweens don't overlap
  if (utils.isMobile()) {
    dispatch('SET_CAMERA_START_ROTATION', cameraEndRotationY)
    startCameraAutoRotation(state, resultY => {
      dispatch('INCREASE_CAMERA_ROTATION', resultY)
    })
    Vue.nextTick(() => {
      // dispatch('SET_PREV_NEXT_ARROWS')
      setTimeout(() => { dispatch('SET_PREV_NEXT_ARROWS') }, 1000)
    })
  } else {
    utils.animate({ startAnimation, endAnimation }, result => {
      dispatch('SET_CAMERA_START_ROTATION', result.y)
    }, () => {
      startCameraAutoRotation(state, resultY => {
        dispatch('INCREASE_CAMERA_ROTATION', resultY)
      })
      Vue.nextTick(() => {
        // dispatch('SET_PREV_NEXT_ARROWS')
        setTimeout(() => { dispatch('SET_PREV_NEXT_ARROWS') }, 1000)
      })
    })
  }
}

