export const stopCameraAutoRotation = ({ dispatch, state }) => {
  // TODO move this to somewhere else?
  dispatch('SET_MARKER_ACTIVE', false)
  dispatch('SET_SELECTED_MARKER', {})
  state.isAutoRotatiing = false
  resetCameraAutoRotation(state)
  const rotationTimer = setTimeout(() => {
    startCameraAutoRotation(state, resultY => {
      dispatch('INCREASE_CAMERA_ROTATION', resultY)
    })
  }, 20000)
  dispatch('SET_ROTATION_TIMER', rotationTimer)
  if (utils.isMobile()) {
    if (state.isMarkerActive) dispatch('SET_MARKER_ACTIVE', false)
  }
}

