export const authWithFirebase = ({ dispatch, state }, providerName, callback) => {
  let provider
  switch (providerName) {
    case 'google-plus':
      provider = new firebase.auth.GoogleAuthProvider()
      break
    case 'facebook':
      provider = new firebase.auth.FacebookAuthProvider()
      break
  }
  firebase.auth().signInWithPopup(provider).then(result => {
    var token = result.credential.accessToken
    var user = result.user
    console.log('user: ' + user + ' token: ', token)
    dispatch('SET_FIREBASE_UID', user.uid)
    localStorage.setItem(LOCAL_ITEM_SHARE_LIKE + '_' + state.buildingHash, false) // eslint-disable-line
    localStorage.setItem(LOCAL_ITEM_FIREBASE_UID, user.uid) // eslint-disable-line
    var likeRef = firebase.database().ref('likes/' + state.buildingHash)
    likeRef.once('value', function (snapshot) {
      if ((snapshot.numChildren() !== 0) && (user.uid in snapshot.val())) {
        dispatch('SET_FIREBASE_LIKE', true)
        localStorage.setItem(LOCAL_ITEM_SHARE_LIKE + '_' + state.buildingHash, true) // eslint-disable-line
      } else {
        dispatch('SET_FIREBASE_LIKE', false)
        localStorage.setItem(LOCAL_ITEM_SHARE_LIKE + '_' + state.buildingHash, false) // eslint-disable-line
      }
    })
    if (callback) callback()
  }).catch((err) => {
    console.log('Auth with Google err: ', err)
  })
}

export const fetchFirebaseBuilding = ({ dispatch, state }, shareCode) => {
  const buildingRef = firebase.database().ref('buildings/' + shareCode)
  buildingRef.once('value', function (snapshot) {
    if (snapshot.numChildren() === 0) {
      // set default value for building and child data
      const defaultBuildingVal = {
        likeCount: 0,
        viewCount: 0,
        commentCount: 0
      }
      buildingRef.set(defaultBuildingVal)
      dispatch('FETCH_FIREBASE_BUILDING', defaultBuildingVal)
    } else {
      dispatch('FETCH_FIREBASE_BUILDING', snapshot.val())
    }
  })
}

