export const fetchFirebaseComments = ({ dispatch, state }, shareCode, limit) => {
  const commentsRef = firebase.database().ref('comments/' + shareCode)
  let commentsLimit = 0
  limit
    ? commentsLimit = limit
    : commentsLimit = 20
  commentsRef.orderByChild('date')
    .limitToLast(commentsLimit)
    .once('value', function (snapshot) {
      const firebaseComments = snapshot.val()
      let comments = []
      for (let children in firebaseComments) {
        comments.push(firebaseComments[children])
      }
      // const commentsLimit = 50
      // const lastComments = comments.slice(Math.max(comments.length - commentsLimit))
      // console.log(lastComments.length)
      // dispatch('FETCH_FIREBASE_COMMENTS', lastComments)
      dispatch('FETCH_FIREBASE_COMMENTS', comments)
    })
}

