export const increateFirebaseViewCount = ({ dispatch, state }, shareCode) => {
  const buildingRef = firebase.database().ref('buildings/' + shareCode)
  const viewCountRef = firebase.database().ref('buildings/' + shareCode + '/viewCount')
  buildingRef.once('value', function (snapshot) {
    if (snapshot.numChildren() === 0) {
      const defaultBuildingVal = {
        likeCount: 0,
        viewCount: 1,
        commentCount: 0
      }
      buildingRef.set(defaultBuildingVal)
    } else {
      viewCountRef.transaction(function (currViewCount) {
        dispatch('INCREASE_FIREBASE_VIEW_COUNT', currViewCount)
        return currViewCount + 1
      })
    }
  })
}

export const onClickFirebaseLikeCount = ({ dispatch, state }) => {
  const likeCountRef = firebase.database().ref('buildings/' + state.building.shareCode + '/likeCount')
  const likesRef = firebase.database().ref('likes/' + state.building.shareCode)
  if (state.isFirebaseLiked) {
    likeCountRef.transaction(function (currLikeCount) {
      dispatch('DECREASE_FIREBASE_LIKE_COUNT', currLikeCount)
      dispatch('SET_FIREBASE_LIKE', false)
      localStorage.setItem(LOCAL_ITEM_SHARE_LIKE + '_' + state.buildingHash, false) // eslint-disable-line

      if (state.firebaseUid) {
        likesRef.child(state.firebaseUid).remove()
      }
      return currLikeCount - 1
    })
  } else {
    likeCountRef.transaction(function (currLikeCount) {
      dispatch('INCREASE_FIREBASE_LIKE_COUNT', currLikeCount)
      dispatch('SET_FIREBASE_LIKE', true)
      localStorage.setItem(LOCAL_ITEM_SHARE_LIKE + '_' + state.buildingHash , true) // eslint-disable-line
      return currLikeCount + 1
    })

    likesRef.once('value', function (snapshot) {
      // TODO
      const value = snapshot.val() || {}
      const uid = state.firebaseUid || 'Anonymous'
      value[uid] = 1 // rating?
      likesRef.update(value)
    })
  }
}

