export const addFirebaseComment = ({ dispatch, state }) => {
  const shareCode = state.building.shareCode
  const commentCountURL = 'buildings/' + shareCode + '/commentCount'
  const commentCountRef = firebase.database().ref(commentCountURL)
  commentCountRef.transaction(function (currCommentCount) {
    return currCommentCount + 1
  })

  let uuid = 'uuid'
  let author = 'Anonymous'
  const currentUser = firebase.auth().currentUser
  if (currentUser !== null) {
    uuid = currentUser.uid
    author = currentUser.email
  }
  const commentsRef = firebase.database().ref('comments/' + shareCode)
  commentsRef.push({
    author: author,
    message: state.firebaseCurrMsg,
    uuid: uuid,
    date: moment().format()
  })
  dispatch('RESET_FIREBASE_CURR_MESSAGE')
}

