export const logout = ({ dispatch }) => {
  firebase.auth().signOut()
  for (let key in localStorage) { // eslint-disable-line
    if (key.substring(0, 9) === 'firebase:') {
      localStorage.removeItem(key) // eslint-disable-line
    }
  }
  localStorage.removeItem('istaging_firebase_uid') // eslint-disable-line
  localStorage.removeItem('istaging_sharevr_logged_in') // eslint-disable-line
  dispatch('LOGOUT')
}

