function startCameraAutoRotation (state, callback) {
  state.isAutoRotatiing = true
  const startAnimation = { y: state.cameraRotation.y }
  const endAnimation = { y: state.cameraRotation.y + 360 }
  utils.animate({
    startAnimation,
    endAnimation,
    speed: 200000,
    repeat: true,
    noEasing: true
  }, (result, tween) => {
    state.rotationTween = tween // need this to stop/reset the animation
    callback(result.y)
  })
}

function resetCameraAutoRotation (state) {
  state.rotationTween.stop()
  clearTimeout(state.rotationTimer)
}

