<template>
  <article>
    <i class="fa fa-close" @click="backToShowcase(panosEl)"></i>
    <div>
      <i class="fa fa-mobile"></i>
      <i class="fa fa-simplybuilt"></i>
      <p>
        {{{ $t('vrInstructions') }}}
      </p>
    </div>
  </article>
</template>

<script>
import { isVRToggled, panosEl } from '../vuex/getters'
import {
  toggleVR,
  exitVR,
  toggleInstructions,
  closeInstructions,
  backToShowcase
} from '../vuex/actions'

export default {
  vuex: {
    getters: {
      isVRToggled,
      panosEl
    },
    actions: {
      toggleVR,
      exitVR,
      toggleInstructions,
      closeInstructions,
      backToShowcase
    }
  },

  ready () {
    window.addEventListener('resize', this.toggleInstructions, false)
  }
}
</script>

<style lang="stylus" scoped>
@import '../variables'

@include keyframes(bounce) {
	0% {
    @include transform(translateY(0))
  }
	60% {
    @include transform(translateY(-300px))
  }
}

article {
  background: $gray-light
  color: $white
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  z-index: 2

  > i {
    cursor: pointer
    font-size: 18px // TODO turn this into a variable
    line-height: 48px
    text-align: center
    width: 48px
    height: 48px
    position: absolute
  }

  > div {
    width: auto
    height: auto
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -60%)
    text-align: center

    p {
      color: alpha($white, 0.6)
      margin-top: 10px

      strong {
        color: $white
        font-weight: 400
      }
    }

    i {
      display: block

      &:nth-child(1) {
        color: $white
        font-size: 84px
        opacity: 0.5
        position: relative
        text-shadow: 0 1px 12px rgba(0,0,0,0.3)
        /*transform: rotate(90deg)*/
        -webkit-animation: rotateDown 1.5s infinite normal ease-out
        z-index: 1
      }

      &:nth-child(2) {
        opacity: 0.8
        position: relative
        text-shadow: 0 1px 12px rgba(0,0,0,0.3)
        font-size: 50px
        z-index: 2
      }
    }
  }

  @-webkit-keyframes rotateDown {
    0% {
      top: 0
    }
    100% {
      top: 70px
      transform: rotate(90deg)
    }
  }
}
</style>



