<template>
<div class="thumbs-wrapper">
  <a
    class="left-arrow"
    @click="slideThumbs('left')">
    <i class="fa fa-caret-left"></i>
  </a>
  <ul
    v-el:thumbs
    class="thumbs list-inline"
    :style="thumbsPosition">
    <li v-for="pano in panos" v-show="isActiveCategory(pano)">
      <a
        class="thumb"
        :class="[{ disabled: this.isPanoChanging},isSelectedOrHovered(pano)]"
        :style="{ backgroundImage: 'url(' + pano.urlThumbnail + ')' }"
        @click="selectPano(pano)"
        @mouseover="setHoveredPano(pano)"
        @mouseleave="setHoveredPano(null)"
        v-touch:swipeleft="slideThumbs('right')"
        v-touch:swiperight="slideThumbs('left')">
        <span>{{ $t(pano.category) }}</span>
      </a>
    </li>
  </ul>
  <a
    class="right-arrow"
    @click="slideThumbs('right')">
    <i class="fa fa-caret-right"></i>
  </a>
</div>
</template>

<script>
import utils from '../../utils'

import {
  panos,
  backPano,
  frontPano,
  selectedPano,
  hoveredPano,
  selectedCategory,
  translateX,
  cameraEl,
  frontPanoEl,
  backPanoEl,
  isPanoChanging
} from '../../vuex/getters'

import {
  slideThumbs,
  setSelectedPano,
  setSelectedMarker,
  setMarkerActive,
  setCameraPosition,
  setHoveredPano,
  setCameraStartRotation,
  loadFullPano,
  animateOpacity,
  setFrontPano,
  setBackPano,
  increaseCameraRotation,
  stopCameraAutoRotation,
  getThumbsEl,
  setShouldUsePanoRotation
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      panos,
      backPano,
      frontPano,
      selectedPano,
      hoveredPano,
      translateX,
      selectedCategory,
      cameraEl,
      frontPanoEl,
      backPanoEl,
      isPanoChanging
    },
    actions: {
      slideThumbs,
      setSelectedPano,
      setSelectedMarker,
      setMarkerActive,
      setCameraPosition,
      setHoveredPano,
      setCameraStartRotation,
      loadFullPano,
      animateOpacity,
      setFrontPano,
      setBackPano,
      increaseCameraRotation,
      stopCameraAutoRotation,
      getThumbsEl,
      setShouldUsePanoRotation
    }
  },

  computed: {
    thumbsPosition () {
      return {
        transform: 'translate(' + this.translateX + 'px, 0px)'
      }
    }
  },

  ready () {
    this.getThumbsEl(this.$els.thumbs)
  },

  methods: {
    selectPano (pano) {
      if (pano !== this.selectedPano && !this.isPanoChanging) {
        // TODO find a better way to reference these elements
        this.setBackPano(pano)
        this.setFrontPano(this.selectedPano)
        this.setSelectedPano(pano)
        this.setShouldUsePanoRotation(true)
        this.setMarkerActive(false)
        this.setSelectedMarker({})
        this.loadFullPano(this.selectedPano)
        // this.setCameraPosition(pano)
        this.setCameraStartRotation(this.cameraEl)
        utils.animateOpacity(pano, this.frontPanoEl, this.backPanoEl)
      }
    },

    isActiveCategory (pano) {
      return utils.isActive(pano.category, this.selectedCategory)
    },

    isSelectedOrHovered (pano) {
      const isSelectedPano = pano === this.selectedPano
      const isHoveredPano = pano === this.hoveredPano
      if (isSelectedPano) return 'selected'
      if (isHoveredPano) return 'hovered'
    }
  }
}
</script>

<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.thumbs-wrapper {
  overflow: hidden
  position: relative
  left: 0
  top: 0
  white-space: nowrap
}

.left-arrow,
.right-arrow {
  /*background: $default-dark*/
  cursor: pointer
  display: block
  width: 48px
  height: 48px
  position: absolute
  text-align: center
  z-index: 9999
  black-text-shadow()

  &:hover {
    i {
      brand-text-shadow()
    }
  }

  i {
    color: $white
    font-size: 28px
    line-height: 48px
  }
}

.left-arrow {
  top: 50%
  left: 0
  transform: translateY(-50%)
}

.right-arrow {
  top: 50%
  right: 0
  transform: translateY(-50%)
}

.thumbs,
.thumb {
  height: 70px // prevent invisible bottom margin
}

.thumbs {
  margin: 17px 0 // 17px for the top shadow
  /*transition: all .1s ease*/

  li {
    cursor: pointer
    padding: 0
  }

  .thumb {
    background-size: 100% 100%
    border: 1px solid $white
    display: block
    position: relative
    background-repeat: no-repeat
    width: 130px
    margin: 0 7px
    -webkit-user-drag: none
    -khtml-user-drag: none
    -moz-user-drag: none
    -o-user-drag: none
    user-drag: none

    &:hover {
      brand-box-shadow()
    }

    span {
      background: rgba(0,0,0,0.5)
      color: $white
      display: block
      font-size: 13px
      padding: 3px
      position: absolute
      bottom: 0
      line-height: 18px
      text-align: center
      width: 100%
    }

    &.selected {
      cursor: default
      border-color: $brand-color
      brand-box-shadow()
    }

    &.hovered {
      brand-box-shadow()
    }

    &.disabled {
      cursor: default
    }
  }
}
</style>



