<template>
  <a-entity
    id="camera-container"
    v-el:camera-container
    :position="joinedCameraPosition"
    :rotation="joinedCameraRotation"
    @componentchanged="onCameraChange($event, 'cameraContainer')">
    <a-camera
      id="camera"
      v-el:camera
      mouse-cursor
      @componentchanged="onCameraChange($event, 'camera')">
      <a-cursor
        v-if="isVRToggled"
        class="cursor"
        fuse="true"
        position="0 0 -1"
        max-distance="999999"
        geometry="primitive: ring"
        scale="1.5 1.5 1.5"
        :color="cursorColor"
        timeout="2000"
        shader="flat">
      </a-cursor>
    </a-camera>
  </a-entity>
</template>

<script>
import utils from '../../utils'
import 'aframe-mouse-cursor-component'
import {
  isVRToggled,
  isFullScreen,
  isTagClick,
  isAutoRotatiing,
  cameraPosition,
  cameraRotation,
  selectedPano
} from '../../vuex/getters'

import {
  setMarkerRotation,
  setMarkerActive,
  setTagClick,
  getCameraEl
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      isVRToggled,
      isFullScreen,
      isTagClick,
      isAutoRotatiing,
      cameraPosition,
      cameraRotation,
      selectedPano
    },
    actions: {
      setMarkerRotation,
      setMarkerActive,
      setTagClick,
      getCameraEl
    }
  },

  data () {
    return {
      cursorColor: '#45b29d',
      dragMoveProps: ''
    }
  },

  computed: {
    joinedCameraPosition () {
      return `${this.cameraPosition.x} ${this.cameraPosition.y} ${this.cameraPosition.z}`
    },

    joinedCameraRotation () {
      return `${this.cameraRotation.x} ${this.cameraRotation.y} ${this.cameraRotation.z}`
    }
  },

  ready () {
    this.setDragMove()
    this.getCameraEl(this.$els.camera)
    // const worldPosition = (new THREE.Vector3()).copy(this.$els.camera.getAttribute('position'))
    // console.log('WORLD.POSS', worldPosition)
  },

  methods: {
    setDragMove () {
      if (!utils.isMobile()) this.dragMoveProps = 'enabled: true'
      else this.dragMoveProps = 'enabled: false'

      // if (this.isTagClick) {
      //   this.dragMoveProps = 'enabled: false'
      //   console.log('in')
      // } else {
      //   this.dragMoveProps = 'enabled: true'
      //   console.log('in 2')
      // }
    },

    onCameraChange (event, src) {
      // console.log(event.detail)
      if ((src === 'cameraContainer' && this.isAutoRotatiing) || (src === 'camera' && !this.isAutoRotatiing)) {
        const camera = src === 'camera' ? this.$els.camera : this.$els.cameraContainer
        const oldData = event.detail.oldData
        const newData = event.detail.newData
        // console.log('oldData: ', oldData)
        // console.log('newData: ', newData)
        if (!oldData) return
        const is360deg = (/360\.(jpg|jpeg|png)$/).test(this.selectedPano.url)
        const hasDataChanged = oldData.x !== newData.x
        // const is720deg = (/720\.(jpg|jpeg|png)$/).test(selectedPano.url)
        // need this otherwise the event will run forever
        this.setMarkerRotation(camera)
        if (!utils.isMobile() && this.isTagClick && (Math.abs(oldData.y) !== Math.abs(newData.y))) {
          this.setMarkerActive(false)
          this.setTagClick(false)
        }
        if (hasDataChanged && is360deg && !this.isVRToggled) {
          camera.setAttribute('rotation', { x: 0, y: newData.y, z: 0 })
        }
      }
    }
  }
}
</script>



