<template>
  <div class="messaging">
    <div class="comments" v-el:comments>
      <div class="comment" v-for="firebaseComment in firebaseComments
        | orderBy 'date'">
        <img :src="avatarImage">
        <div class="comment-details">
          <h5 class="disabled">
            {{ firebaseComment.author }}
            <small>{{ relativizeDate(firebaseComment.date) }}</small>
          </h5>
          <p>{{ firebaseComment.message }}</p>
        </div>
      </div>
    </div>
    <form @submit.stop.prevent>
      <div class="row">
        <div class="col-lg-12">
          <div class="input-group">
            <input
              type="text"
              class="form-control"
              :value="firebaseCurrMsg"
              @input="updateFirebaseCurrMessage"
              @keyup.13="addComment">
            <span class="input-group-btn">
              <button
                class="btn btn-default"
                type="button"
                @click="addComment">
                {{ $t('send') }}
              </button>
            </span>
          </div>
        </div>
      </div>
    </form>
    <a
      class="logout muted"
      v-if="isFirebaseLoggedIn"
      @click="logout">
      {{ $t('logOut') }}
    </a>
  </div>
</template>

<script>
import moment from 'moment'
import firebase from 'firebase'
import {
  firebaseComments,
  firebaseCurrMsg,
  commentsEl,
  isFirebaseLoggedIn
} from '../../vuex/getters'

import {
  fetchFirebaseComments,
  addFirebaseComment,
  updateFirebaseCurrMessage,
  toggleAuthModal,
  getCommentsEl,
  logout
} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      firebaseComments,
      firebaseCurrMsg,
      commentsEl,
      isFirebaseLoggedIn
    },
    actions: {
      fetchFirebaseComments,
      addFirebaseComment,
      updateFirebaseCurrMessage,
      toggleAuthModal,
      getCommentsEl,
      logout
    }
  },

  data () {
    return {
      avatarImage: require('../../assets/avatar.png')
    }
  },

  ready () {
    const buildingHash = this.$route.params.buildingHash
    this.getCommentsEl(this.$els.comments)
    const commentsRef = firebase.database().ref('comments/' + buildingHash)
    commentsRef.on('child_added', snapshot => {
      this.fetchFirebaseComments(buildingHash)
    })
    commentsRef.on('child_removed', snapshot => {
      this.fetchFirebaseComments(buildingHash)
    })
    this.commentsEl.addEventListener('scroll', () => {
      if (this.commentsEl.scrollTop === 0) this.fetchMoreComments()
    })
    this.fetchFirebaseComments(buildingHash)
  },

  methods: {
    addComment () {
      if (this.firebaseCurrMsg.length === 0) return
      if (this.isFirebaseLoggedIn) {
        this.addFirebaseComment()
        this.$nextTick(() => {
          this.commentsEl.scrollTop = this.commentsEl.scrollHeight
        })
      } else {
        this.toggleAuthModal()
      }
    },

    fetchMoreComments () {
      const buildingHash = this.$route.params.buildingHash
      const commentsCount = this.firebaseComments.length
      // const commentsScrollHeight = this.commentsEl.scrollHeight
      this.fetchFirebaseComments(buildingHash, commentsCount + 20)
      const newCommentsCount = this.firebaseComments.length
      const newCommentsScrollHeight = this.commentsEl.scrollHeight
      if (commentsCount !== newCommentsCount) {
        const totalScrollHeight = newCommentsScrollHeight
        this.$nextTick(() => { this.commentsEl.scrollTop = totalScrollHeight })
      }
    },

    relativizeDate (date) {
      return moment(date).fromNow()
    }
  }
}
</script>
<style lang="stylus">
@import '../../variables'
@import '../../mixins'

.logout {
  position: absolute
  top: 0
  right: 0
  display: block
  padding: 15px 45px 0 0
  margin: 0
  cursor: pointer
}
</style>



