<template>
  <div class="row">
    <div id="ui" v-if="!isBuildingDeleted">
      <h2 v-show="shouldShowUI" v-if="!ui">
        {{ building.name }}
        <!-- <br> -->
        <!-- <small>({{ building.shareCode }})</small> -->
      </h2>
      <pano-social v-if="firebaseBuilding && shouldShowUI && !ui"></pano-social>
      <pano-user v-show="shouldShowUI && !ui"></pano-user>
      <!-- TODO remove categories if not used in the future -->
      <pano-categories v-show="shouldShowUI && !ui"></pano-categories>
      <pano-buttons v-show="!isVRToggled && !ui"></pano-buttons>
      <pano-floorplan v-show="shouldShowUI" v-if="!ui"></pano-floorplan>
      <pano-map v-if="isMapCreated && !ui"></pano-map>
      <pano-background></pano-background>
      <pano-instructions v-show="showInstructions && !ui"></pano-instructions>
      <pano-nav v-show="shouldShowUI"></pano-nav>
      <pano-modal v-show="isAuthModalActive && !ui"></pano-modal>
      <div class="loading">
        <pano-spinner></pano-spinner>
        <img id="logo" v-if="!havePanosLoaded" :src="logo">
      </div>
      <div class="power-by" v-show="!ui">Powered by iStaging</div>
    </div>
    <div id="no-ui" v-if="isBuildingDeleted">
      <i class="fa fa-frown-o"></i>
      <h3>{{ $t('noBuilding') }}</h3>
    </div>
    <!-- to hide the UI elements before the data is ready -->
    <div v-if="panos && panos.length === 0" id="overlay"></div>
  </div>
</template>

<script>
import 'aframe'
import 'custom-event-polyfill'
import PanoSocial from '../components/PanoSocial'
import PanoBackground from '../components/PanoBackground'
import PanoCategories from '../components/PanoCategories'
import PanoUser from '../components/PanoUser'
import PanoMap from '../components/PanoMap'
import PanoFloorplan from '../components/PanoFloorplan'
import PanoButtons from '../components/PanoButtons'
import PanoNav from '../components/PanoNav'
import PanoModal from '../components/PanoModal'
import PanoSpinner from '../components/PanoSpinner'
import PanoInstructions from '../components/PanoInstructions'
import {
  panos,
  building,
  buildingHash,
  firebaseBuilding,
  showInstructions,
  isVRToggled,
  isFullScreen,
  isMapCreated,
  isAuthModalActive,
  isAppBuilding
} from '../vuex/getters'

import {
  getBuildingHash,
  fetchFirebaseBuilding
} from '../vuex/actions'

export default {
  name: 'PanoContainer',
  vuex: {
    getters: {
      panos,
      building,
      buildingHash,
      firebaseBuilding,
      showInstructions,
      isVRToggled,
      isFullScreen,
      isMapCreated,
      isAuthModalActive,
      isAppBuilding
    },
    actions: {
      getBuildingHash,
      fetchFirebaseBuilding
    }
  },

  components: {
    PanoSocial,
    PanoBackground,
    PanoCategories,
    PanoUser,
    PanoMap,
    PanoFloorplan,
    PanoButtons,
    PanoNav,
    PanoModal,
    PanoSpinner,
    PanoInstructions
  },

  data () {
    return {
      logo: require('../assets/logo.png'),
      isBuildingDeleted: false
    }
  },

  computed: {
    ui () {
      return this.$route.query.ui
    },

    hasFloorplan () {
      return this.building.floorplan
    },

    shouldShowUI () {
      return !this.isVRToggled && !this.isFullScreen
    },

    havePanosLoaded () {
      if (!this.panos) return
      return this.panos.length > 0
    }
  },

  created () {
    const buildingHash = this.$route.params.buildingHash
    this.getBuildingHash(buildingHash)
    this.fetchFirebaseBuilding(buildingHash)
    this.$lang.lang
    if (Function('/*@cc_on return document.documentMode===10@*/ ')()) { // eslint-disable-line
    // if (!(window.ActiveXObject) && 'ActiveXObject' in window) {
      this.insertNoSupported()
    }
  },

  watch: {
    building () {
      console.log('SHAREDCODE:', this.building)
      if (!this.building.shareCode) this.isBuildingDeleted = true
    }
  },

  methods: {
    isFirstPano (pano) {
      return pano === this.panos[0]
    },

    insertNoSupported () {
      const containerStyle = 'text-align: center; margin-top: 280px;'
      const noSupportedImage = require('../assets/sorry.png')
      let noSupportedMessage = null
      if (this.$lang.lang === 'en') {
        noSupportedMessage = `<div style="${containerStyle}">
          <img src="${noSupportedImage}">
          <h3>Sorry, your browser is no supported.</h3>
          <p>Please download and install a modern browser like
            <a href="https://www.google.com/chrome/browser/desktop">Chrome</a>,
            <a href="https://www.microsoft.com/en-us/download/internet-explorer-11-for-windows-7-details.aspx">IE11</a>, or
            <a href="https://www.mozilla.org/en-US/firefox/new/">Firefox</a>.
          </p>`
      }
      if (this.$lang.lang === 'zh') {
        noSupportedMessage = `<div style="${containerStyle}">
          <img src="${noSupportedImage}">
          <h3>對不起，您的瀏覽器不支持。</h3>
          <p>請下載並安裝最新的瀏覽器版本，如<!--
            --><a href="https://www.google.com/chrome/browser/desktop">Chrome</a>、<!--
            --><a href="https://www.microsoft.com/en-us/download/internet-explorer-11-for-windows-7-details.aspx">IE11</a>或<!--
            --><a href="https://www.mozilla.org/en-US/firefox/new/">Firefox</a>。
          </p>`
      }
      document.getElementById('app').innerHTML += noSupportedMessage
    }
  }
}
</script>

<style lang="stylus">
@import '../variables'
@import '../mixins'

h2 {
  color: $white
  font-size: 20px
  margin: 0
  position: fixed
  top: 15px
  left: 50%
  transform: translateX(-50%)
  text-align: center
  z-index: 1
  white-space: nowrap
  heavy-text-shadow()

  small {
    font-size: 16px
    color: $white
  }
}

.loading {
  margin-top: -40px
  position: fixed
  left: 50%
  top: 56%
  transform: translate(-50%, -50%)
  z-index: 9999

  #logo {
    width: 144px
    // to remove the blurriness
    -webkit-backface-visibility: hidden
    -webkit-transform: translateZ(0) scale(1.0, 1.0)
  }
}

.power-by {
  position: absolute
  color: white
  font-size: 20px
  bottom: 20px
  left: 20px

  @media screen and (max-width: 1280px) {
    display: none
  }
}

#no-ui {
  position: fixed
  top: 50%
  left: 50%
  transform: translate(-50%,-50%)
  text-align: center

  i {
    color: $brand-color
    font-size: 60px
    margin-left: auto
    margin-right: auto
  }
}

#overlay {
  background: white
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  z-index: 9
}
</style>



