import Vue from 'vue'
import VueI18n from 'vue-i18n'
import Router from 'vue-router'
import { sync } from 'vuex-router-sync'
import Resource from 'vue-resource'
import Touch from 'vue-touch'
import locales from './locales'
import { camelCaseToText } from './filters'
import { toggle, toggleSiblings, slide, slideRelative } from './directives'
import App from './App'
import PanoContainer from './components/PanoContainer'
import store from './vuex/store'

Vue.use(VueI18n)
Vue.use(Router)
Vue.use(Resource)
Vue.use(Touch)

Vue.filter('camelCaseToText', camelCaseToText)
Vue.directive('toggle', toggle)
Vue.directive('toggle-siblings', toggleSiblings)
Vue.directive('slide', slide)
Vue.directive('slide-relative', slideRelative)

Vue.config.lang = 'en'

Object.keys(locales).forEach(lang => {
  Vue.locale(lang, locales[lang])
})

var router = new Router()

router.map({
  '/:buildingHash': {
    component: PanoContainer
  }
})

router.beforeEach(function () {
  window.scrollTo(0, 0)
})

router.redirect({
  '*': '/'
})

sync(store, router)

router.start(App, '#app')



