<template>
  <a-entity>
    <a-ring
      :src="building.logo ? building.logo : logoTripod"
      color="gray"
      metalness="0"
      radius-inner=".01"
      :radius-outer="getLogoSize()"
      rotation="-90 0 0"
      position="0 -40 0">
    </a-ring>
  </a-entity>
</template>

<script>
import {
  building
} from '../../vuex/getters'

import {

} from '../../vuex/actions'

export default {
  vuex: {
    getters: {
      building
    },
    actions: {

    }
  },

  data () {
    return {
      logoTripod: require('../../assets/logo-tripod.png')
    }
  },

  methods: {
    getLogoSize () {
      let logoPercent = 12
      switch (this.building.logoPercent) {
        case '100':
          logoPercent = 12
          break
        case '125':
          logoPercent = 15
          break
        case '150':
          logoPercent = 18
          break
        case '200':
          logoPercent = 24
          break
        default:
          break
      }
      return logoPercent
    }
  }
}
</script>



